import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute, ROUTER_DIRECTIVES } from "@angular/router";
import {Http, Response} from "@angular/http";


import { Account } from "../account/account.model";
import { Patient, Allergy, Addiction, BloodPressure, HeartBeat, BloodSugar } from "./patient.model";
import { Doctor } from "../doctor/doctor.model";

import { patientRepo } from "../utils/data/patients";
import { bloodPressureRepo } from "../utils/data/bloodpressure";
import { heartBeatRepo } from "../utils/data/heartbeat";
import { sugarBloodRepo } from "../utils/data/sugarblood";

@Component({
    selector: 'patient-stats-bp',
    template: require("./patient.stats.bp.component.html"),
    styles: [
        require("../../sass/pages/stats.bp.scss")
    ],
    directives: [ROUTER_DIRECTIVES],
})
export class StatsBPPatientComponent implements OnInit {
    private account: Account;
    private patient: Patient;

    private bloodPressureList: BloodPressure[] = [];
    private bloodPressure: BloodPressure = new BloodPressure();
    private edBloodPressure: BloodPressure = new BloodPressure();

    constructor(private router: Router,
        private activatedRoute: ActivatedRoute) {
    }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));

        // Data
        this.bloodPressureList = bloodPressureRepo.getAll();

        this.drawBloodPressureChart();
    }

    drawBloodPressureChart() {
        let chartContainer = document.getElementById('bp-chart');
        var data = [{
            "sale": "202",
            "year": "2000"
        }, {
                "sale": "215",
                "year": "2001"
            }, {
                "sale": "179",
                "year": "2002"
            }, {
                "sale": "199",
                "year": "2003"
            }, {
                "sale": "134",
                "year": "2003"
            }, {
                "sale": "176",
                "year": "2010"
            }];
        // let visual = d3.select("#visualisation")    

    }
    // START BloodPressure
    toggleBloodPressureContent() {
        let bpVisual = document.getElementById('bp-visual');
        let bpHistory = document.getElementById('bp-history');
        if (bpVisual.style.display == 'block') {
            bpVisual.style.display = 'none';
            bpHistory.style.display = 'none';

            let bpAngle = document.getElementById('bp-angle');
            let iElem = <HTMLElement>document.createElement('i');
            iElem.className += 'fa fa-angle-double-down';
            iElem.setAttribute('aria-hidden', 'true');
            bpAngle.innerHTML = '';
            bpAngle.appendChild(iElem);

            // Close bp history
            let bpHistoryContent = document.getElementById('bp-history-content');
            if (bpHistoryContent.style.display == 'block') {
                bpHistoryContent.style.display = 'none';

                let bpAngle = document.getElementById('bp-content-angle');
                let iElem = <HTMLElement>document.createElement('i');
                iElem.className += 'fa fa-angle-down';
                iElem.setAttribute('aria-hidden', 'true');
                bpAngle.innerHTML = '';
                bpAngle.appendChild(iElem);
            }
        } else {
            bpVisual.style.display = 'block';
            bpHistory.style.display = 'block';

            let bpAngle = document.getElementById('bp-angle');
            let iElem = <HTMLElement>document.createElement('i');
            iElem.className += 'fa fa-angle-double-up';
            iElem.setAttribute('aria-hidden', 'true');
            bpAngle.innerHTML = '';
            bpAngle.appendChild(iElem);
        }
    }

    toggleBPHistoryContent() {
        let bpHistoryContent = document.getElementById('bp-history-content');
        if (bpHistoryContent.style.display == 'block') {
            bpHistoryContent.style.display = 'none';

            let bpAngle = document.getElementById('bp-content-angle');
            let iElem = <HTMLElement>document.createElement('i');
            iElem.className += 'fa fa-angle-down';
            iElem.setAttribute('aria-hidden', 'true');
            bpAngle.innerHTML = '';
            bpAngle.appendChild(iElem);
        } else {
            bpHistoryContent.style.display = 'block';

            let bpAngle = document.getElementById('bp-content-angle');
            let iElem = <HTMLElement>document.createElement('i');
            iElem.className += 'fa fa-angle-up';
            iElem.setAttribute('aria-hidden', 'true');
            bpAngle.innerHTML = '';
            bpAngle.appendChild(iElem);
        }
    }

    saveNewBloodPressure() {

        // Close edit appointment container
        let bpForm = document.getElementById('bp-form');
        bpForm.style.display = "none";
        let btnAdd = document.getElementById('btn-add-bp');
        btnAdd.style.opacity = '1';
        btnAdd.style.pointerEvents = 'auto';
        return false;
    }

    openAddNewBloodPressure() {
        this.bloodPressure = new BloodPressure();

        let bpForm = document.getElementById('bp-form');
        bpForm.style.display = "block";
        let btnAdd = document.getElementById('btn-add-bp');
        btnAdd.style.opacity = '0.6';
        btnAdd.style.pointerEvents = 'none';

        return false;
    }

    closeAddNewBloodPressure() {
        let bpForm = document.getElementById('bp-form');
        bpForm.style.display = "none";
        let btnAdd = document.getElementById('btn-add-bp');
        btnAdd.style.opacity = '1';
        btnAdd.style.pointerEvents = 'auto';
        return false;
    }
    
    closeEditBloodPressureForm(index: number) {
        let editBPForm = document.getElementById(`ebp-form-${index}`);
        let bpRecord = document.getElementById(`bp-record-${index}`);
        if (editBPForm && bpRecord) {
            editBPForm.style.display = 'none';
            bpRecord.style.display = 'block';
        }

        return false;
    }
    saveEditBloodPressure(index: number) {
        // Save data

        // Update style
        this.closeEditBloodPressureForm(index);

        return false;
    }


    windowClickBPDropdownHandler(event: any) {
        let targetElem = <HTMLElement>event.target;
        if (!targetElem.classList.contains('dropdown-item')
            && !targetElem.classList.contains('btn-submenu-i')) {
            let dropdownSubmenus = document.getElementsByClassName('bp-item-dropdown');
            for (let i = 0; i < dropdownSubmenus.length; i++) {
                let htmlElem = <HTMLElement>dropdownSubmenus[i];
                htmlElem.style.display = "none";
                htmlElem.setAttribute('data-isopened', '0');
            }
        }
    }

    openSubMenuBPItem(index: number) {
        let menuID = `bp-item-${index}`;
        let bpSubmenu = document.getElementById(menuID);
        if (bpSubmenu) {
            let accIsOpened = bpSubmenu.getAttribute('data-isopened');
            if (accIsOpened && accIsOpened == '0') {
                let dropdownSubmenus = document.getElementsByClassName('bp-item-dropdown');
                for (let i = 0; i < dropdownSubmenus.length; i++) {
                    let htmlElem = <HTMLElement>dropdownSubmenus[i];
                    htmlElem.style.display = "none";
                    htmlElem.setAttribute('data-isopened', '0');
                }
                bpSubmenu.style.display = "block";
                bpSubmenu.setAttribute('data-isopened', '1');
                window.addEventListener('click', this.windowClickBPDropdownHandler);
            } else {
                bpSubmenu.style.display = "none";
                bpSubmenu.setAttribute('data-isopened', '0');
                window.removeEventListener('click', this.windowClickBPDropdownHandler);
            }
        }
        return false;
    }

    openEditBPForm(index: number) {
        this.edBloodPressure.id = this.bloodPressureList[index].id;
        this.edBloodPressure.systole = this.bloodPressureList[index].systole;
        this.edBloodPressure.diastole = this.bloodPressureList[index].diastole;
        this.edBloodPressure.note = this.bloodPressureList[index].note;
        this.edBloodPressure.time = this.bloodPressureList[index].time;

        // Change style
        for (let i = 0; i < this.bloodPressureList.length; i++) {
            let editBPForm = document.getElementById(`ebp-form-${i}`);
            if (editBPForm) {
                editBPForm.style.display = 'none';
            }
        }
        for (let i = 0; i < this.bloodPressureList.length; i++) {
            let bpRecord = document.getElementById(`bp-record-${i}`);
            if (bpRecord) {
                bpRecord.style.display = 'block';
            }
        }
        let editBPForm = document.getElementById(`ebp-form-${index}`);
        let bpRecord = document.getElementById(`bp-record-${index}`);
        if (editBPForm && bpRecord) {
            editBPForm.style.display = 'block';
            bpRecord.style.display = 'none';
        }

        return false;
    }

    deleteBPItem(index: number) {
        let result = confirm('Do you really want to delete this blood pressure record ? ');
        if (result) {
            this.bloodPressureList.splice(index, 1);
        }
        return false;
    }
    //END BloodPressure
}


