import {Component, OnInit, AfterViewInit, Input} from "@angular/core";
import {Router, ROUTER_DIRECTIVES } from '@angular/router';
import {Http, Response} from "@angular/http";
import * as moment from "moment";
import { AppointmentOutput } from "./patient.model";
import { PatientAppointmentsServive } from "./patient.appoints.service";
import { Account } from "../account/account.model";

import { ISO8601DateFormat, ISO8601DateTimeFormat} from "../utils/constants";

import { AppointOutRepo } from "../utils/data/appoints";

@Component({
    selector: 'patient-appoint-total',
    template: require("./patient.appoints.activated.component.html"),
    styles: [
        require("../../sass/pages/appoint.activated.scss"),
    ],
    directives: [ROUTER_DIRECTIVES],
    providers: [PatientAppointmentsServive],
})
export class AppointsTotalComponent implements OnInit, AfterViewInit {
    private account: Account;
    private appoints: AppointmentOutput[] = [];
    private activeAppoints: AppointmentOutput[] = [];
    private currentAppoints: AppointmentOutput[] = [];
    private currentDate: string;
    private calendar: any;

    constructor(private router: Router,
        private appointService: PatientAppointmentsServive) {

    }

    ngOnInit() {

        this.account = JSON.parse(window.localStorage.getItem('account'));

        // fake data
        let aRepo: AppointOutRepo = new AppointOutRepo();
        this.appoints = aRepo.getAllAppoints();

        // set active tab
        let liActivatedTabElem = document.getElementById('activated-appoints-tablink').parentElement.parentElement;
        let liPendingTabElem = document.getElementById('pending-appoints-tablink').parentElement.parentElement;
        liActivatedTabElem.className += ' active';
        liPendingTabElem.className = liPendingTabElem.className.replace(' active', '');

        this.calendarSetup();
    }

    ngAfterViewInit() {

    }

    // START add new appointment
    // modelClickAddNewAppointHandler(event: any) {
    //     if (event.target.id == 'add-appoint-modal') {
    //         let proceed = confirm('Do you want to cancel adding new appointment ?');
    //         if (proceed) {
    //             let modal = document.getElementById('add-appoint-modal');
    //             if (modal) {
    //                 modal.style.display = "none";
    //                 modal.removeEventListener('click', this.modelClickAddNewAppointHandler);
    //             }
    //         }
    //     }
    // }

    calendarSetup() {
        let template = '<div class="clndr-controls"><div class="cal-nav"><div class="clndr-today-button"><span>Today</span></div><div class="clndr-previous-button"><i class="fa fa-angle-left" aria-hidden="true"></i></div><div class="clndr-next-button"><i class="fa fa-angle-right" aria-hidden="true"></i></div></div><div class="current-month"> <span><%=month %> <%=year %></span> </div></div><div class="clndr-grid"> <div class="days-of-the-week clearfix"> <% _.each(daysOfTheWeek, function(day){%> <div class="header-day"> <%=day %> </div><%}); %> </div><div class="days"> <% _.each(days, function(day){%> <div class="<%=day.classes %>" id="<%=day.id %>"><span class="day-number"><%=day.day %></span></div><%}); %> </div></div></div>';

        this.currentDate = 'today';

        // get event for today
        this.calendar = (<any>$('#full-clndr')).clndr({
            template: template,
            events: [],
            classes: {
                today: "today",
            },
            clickEvents: {
                click: (target: any) => {
                    this.handleCalendarDayClick(target);
                },
                today: () => this.goToCalendarToday()
            },
        });

        // $('.day').each(function (i, obj) {
        //     obj.classList.remove('chosenday');
        // });
        setTimeout(function() {
            $('.today').addClass('chosenday');
        }, 200);

        // let thisMonth = this.calendar.month;
        // for (let item of this.appoints) {
        //     let startDate = moment(item.start, ISO8601DateTimeFormat);
        //     if (startDate.get('date') == thisMonth.get('date')
        //         && startDate.get('date') == thisMonth.get('date')
        //         && startDate.get('date') == thisMonth.get('date')) {

        //     }
        // }
        let clndrEvents: CLNDREvent[] = [];
        for (let appoint of this.appoints) {
            let item = new CLNDREvent();
            item.title = appoint.partner.firstName + " " + appoint.partner.lastName;
            item.date = moment(appoint.start, ISO8601DateTimeFormat).format('YYYY-MM-DD');
            clndrEvents.push(item);
        }

        if (this.calendar) { 
            this.calendar.setEvents(clndrEvents);
        }
    }

    handleCalendarDayClick(target: any) {
        if (!$(target.element).hasClass('inactive')) {
            let pickedDate: string = target.date.format(ISO8601DateFormat);
            this.currentAppoints = this.filterAppointsByDate(pickedDate);

            // style
            $('.day').each(function (i, obj) {
                obj.classList.remove('chosenday');
            });
            $(target.element).addClass('chosenday');

            // change appointment list title
            if (target.date.get('date') == moment().get('date')
                && target.date.get('month') == moment().get('month')
                && target.date.get('year') == moment().get('year')) {
                this.currentDate = 'today';
            } else {
                this.currentDate = target.date.format(ISO8601DateFormat);
            }

        } else {
            console.log('That date is outside of the range.');
        }
    }

    goToCalendarToday() {
        if (this.calendar) {
            this.calendar.today();
            this.currentAppoints = this.filterAppointsByDate();
            this.currentDate = 'today';

            // style
            $('.day').each(function (i, obj) {
                obj.classList.remove('chosenday');
            });
            $('.today').addClass('chosenday');

        }
    }

    private filterAppointsByDate(date?: string) {
        let mappoints: AppointmentOutput[] = [];
        for (let item of this.appoints) {
            if (date) {
                let dateMoment = moment(date, ISO8601DateFormat);
                let startMoment = moment(item.start, ISO8601DateTimeFormat);
                if (dateMoment.get('date') == startMoment.get('date')
                    && dateMoment.get('month') == startMoment.get('month')
                    && dateMoment.get('year') == startMoment.get('year'))
                    mappoints.push(item);
            } else {
                let currentDate = moment();
                let startMoment = moment(item.start, ISO8601DateTimeFormat);
                if (currentDate.get('date') == startMoment.get('date')
                    && currentDate.get('month') == startMoment.get('month')
                    && currentDate.get('year') == startMoment.get('year'))
                    mappoints.push(item);
            }
        }
        return mappoints;
    }

    diagnostic() {
        return JSON.stringify(this.appoints);
    }
}

class CLNDREvent {
    date: string;
    title: string;
}

