import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute, ROUTER_DIRECTIVES } from "@angular/router";
import {Http, Response} from "@angular/http";

import { Account } from "../account/account.model";
import { Patient, MedicalRecord, MedicalRecordCategory, ExperimentNote, MedicalImage } from "./patient.model";
import { Doctor } from "../doctor/doctor.model";

import { patientRepo } from "../utils/data/patients";
import { medicalRecordRepo, medrecordCategories, expNoteRepo, medImageRepo } from "../utils/data/medrecords";

@Component({
    selector: 'patient-stats',
    template: require("./patient.medrecord.details.component.html"),
    styles: [
        require("../../sass/pages/medrecord.details.scss")
    ],
    directives: [ROUTER_DIRECTIVES]
})
export class MedRecordDetailsPatientComponent implements OnInit {
    private account: Account;
    private patientID = '';
    private patient: Patient;

    private medicalRecord: MedicalRecord = new MedicalRecord();
    private edmedicalRecordModel: MedicalRecord = new MedicalRecord();
    private medCategories: MedicalRecordCategory[] = [];
    private expNotes: ExperimentNote[] = [];
    private medImages: MedicalImage[] = [];

    constructor(private router: Router,
        private activatedRoute: ActivatedRoute) { }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));
        this.patientID = this.activatedRoute.snapshot.params['acid'];
        this.patient = patientRepo.getPatientByEmail(this.patientID);
        
        let medRecordID = this.activatedRoute.snapshot.params['mrid'];
        this.medicalRecord = medicalRecordRepo.getMedicalRecordByID(medRecordID);
        this.medCategories = medrecordCategories;
        this.expNotes = expNoteRepo.getAll();
        this.medImages = medImageRepo.getAll();

    }

    openEditMedicalRecord() {
        this.edmedicalRecordModel.info = this.medicalRecord.info;
        this.medicalRecord.category = this.medicalRecord.category;

        let medContent = document.getElementById('medrecord-geninfo-content');
        let medForm = document.getElementById('medrecord-geninfo-form');
        medContent.style.display = 'none';
        medForm.style.display = 'block';

        return false;
    }   

    deleteMedicalRecord() {
        let answer = confirm('Do you really want to delete this medical record ? ');
        if (answer) {
            this.router.navigate([`/patients/${this.patientID}/medrecords`]);
        }

        return false;
    }

    saveEditMedicalRecord() {

        let medContent = document.getElementById('medrecord-geninfo-content');
        let medForm = document.getElementById('medrecord-geninfo-form');
        medContent.style.display = 'block';
        medForm.style.display = 'none';

        return false;
    }   

    closeEditMedicalRecord() {
        let medContent = document.getElementById('medrecord-geninfo-content');
        let medForm = document.getElementById('medrecord-geninfo-form');
        medContent.style.display = 'block';
        medForm.style.display = 'none';

        return false;
    }

    goToAddNewPrescription() {
        this.router.navigate([`/patients/${this.patientID}/medrecords/${this.medicalRecord.id}/prescriptions/add`]);
        return false;
    }
    goToAddNewExperimentNote() {
        this.router.navigate([`/patients/${this.patientID}/medrecords/${this.medicalRecord.id}/expnotes/add`]);
        return false;
    }

    goToPrescription() {
        this.router.navigate([`/patients/${this.patientID}/prescriptions`]);
        return false;
    }
}


