import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute, ROUTER_DIRECTIVES } from "@angular/router";
import {Http, Response} from "@angular/http";


import { Account } from "../account/account.model";
import { Patient, Allergy, Addiction, BloodPressure, HeartBeat, BloodSugar } from "./patient.model";
import { Doctor } from "../doctor/doctor.model";

import { patientRepo } from "../utils/data/patients";
import { bloodPressureRepo } from "../utils/data/bloodpressure";
import { heartBeatRepo } from "../utils/data/heartbeat";
import { sugarBloodRepo } from "../utils/data/sugarblood";

@Component({
    selector: 'patient-stats-bp',
    template: require("./patient.stats.hb.component.html"),
    styles: [
        require("../../sass/pages/stats.hb.scss")
    ],
    directives: [ROUTER_DIRECTIVES],
})
export class StatsHBPatientComponent implements OnInit {
    private account: Account;
    private patient: Patient;

    private heartBeatList: HeartBeat[] = [];
    private heartBeat: HeartBeat = new HeartBeat();
    private edHeartBeat: HeartBeat = new HeartBeat();

    constructor(private router: Router,
        private activatedRoute: ActivatedRoute) {
    }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));

        // Data
        this.heartBeatList = heartBeatRepo.getAll();

        this.drawBloodSugarChart();
    }

    drawBloodSugarChart() {
        let chartContainer = document.getElementById('bp-chart');
        var data = [{
            "sale": "202",
            "year": "2000"
        }, {
                "sale": "215",
                "year": "2001"
            }, {
                "sale": "179",
                "year": "2002"
            }, {
                "sale": "199",
                "year": "2003"
            }, {
                "sale": "134",
                "year": "2003"
            }, {
                "sale": "176",
                "year": "2010"
            }];
        // let visual = d3.select("#visualisation")    
    }
    // START BloodPressure
    toggleHeartBeatContent() {
        let hbVisual = document.getElementById('hb-visual');
        let hbHistory = document.getElementById('hb-history');
        if (hbVisual.style.display == 'block') {
            hbVisual.style.display = 'none';
            hbHistory.style.display = 'none';

            let hbAngle = document.getElementById('hb-angle');
            let iElem = <HTMLElement>document.createElement('i');
            iElem.className += 'fa fa-angle-double-down';
            iElem.setAttribute('aria-hidden', 'true');
            hbAngle.innerHTML = '';
            hbAngle.appendChild(iElem);

            // Close bp history
            let bpHistoryContent = document.getElementById('hb-history-content');
            if (bpHistoryContent.style.display == 'block') {
                bpHistoryContent.style.display = 'none';

                let bpAngle = document.getElementById('hb-content-angle');
                let iElem = <HTMLElement>document.createElement('i');
                iElem.className += 'fa fa-angle-down';
                iElem.setAttribute('aria-hidden', 'true');
                bpAngle.innerHTML = '';
                bpAngle.appendChild(iElem);
            }
        } else {
            hbVisual.style.display = 'block';
            hbHistory.style.display = 'block';

            let hbAngle = document.getElementById('hb-angle');
            let iElem = <HTMLElement>document.createElement('i');
            iElem.className += 'fa fa-angle-double-up';
            iElem.setAttribute('aria-hidden', 'true');
            hbAngle.innerHTML = '';
            hbAngle.appendChild(iElem);
        }
    }

    toggleHBHistoryContent() {
        let bpHistoryContent = document.getElementById('hb-history-content');
        if (bpHistoryContent.style.display == 'block') {
            bpHistoryContent.style.display = 'none';

            let hbAngle = document.getElementById('hb-content-angle');
            let iElem = <HTMLElement>document.createElement('i');
            iElem.className += 'fa fa-angle-down';
            iElem.setAttribute('aria-hidden', 'true');
            hbAngle.innerHTML = '';
            hbAngle.appendChild(iElem);
        } else {
            bpHistoryContent.style.display = 'block';

            let hbAngle = document.getElementById('hb-content-angle');
            let iElem = <HTMLElement>document.createElement('i');
            iElem.className += 'fa fa-angle-up';
            iElem.setAttribute('aria-hidden', 'true');
            hbAngle.innerHTML = '';
            hbAngle.appendChild(iElem);
        }
    }

    saveNewHeartBeat() {

        // Close edit appointment container
        let bpForm = document.getElementById('hb-form');
        bpForm.style.display = "none";
        let btnAdd = document.getElementById('btn-add-bp');
        btnAdd.style.opacity = '1';
        btnAdd.style.pointerEvents = 'auto';
        return false;
    }

    openAddNewHeartBeat() {
        this.heartBeat = new HeartBeat();

        let bpForm = document.getElementById('hb-form');
        bpForm.style.display = "block";
        let btnAdd = document.getElementById('btn-add-bs');
        btnAdd.style.opacity = '0.6';
        btnAdd.style.pointerEvents = 'none';

        return false;
    }

    closeAddNewHeartBeat() {
        let bpForm = document.getElementById('hb-form');
        bpForm.style.display = "none";
        let btnAdd = document.getElementById('btn-add-bs');
        btnAdd.style.opacity = '1';
        btnAdd.style.pointerEvents = 'auto';
        return false;
    }

    closeEditHeartBeatForm(index: number) {
        let editBSForm = document.getElementById(`ehb-form-${index}`);
        let bsRecord = document.getElementById(`hb-record-${index}`);
        if (editBSForm && bsRecord) {
            editBSForm.style.display = 'none';
            bsRecord.style.display = 'block';
        }

        return false;
    }
    saveEditHeartBeat(index: number) {
        // Save data

        // Update style
        this.closeEditHeartBeatForm(index);

        return false;
    }

    windowClickHBDropdownHandler(event: any) {
        let targetElem = <HTMLElement>event.target;
        if (!targetElem.classList.contains('dropdown-item')
            && !targetElem.classList.contains('btn-submenu-i')) {
            let dropdownSubmenus = document.getElementsByClassName('hb-item-dropdown');
            for (let i = 0; i < dropdownSubmenus.length; i++) {
                let htmlElem = <HTMLElement>dropdownSubmenus[i];
                htmlElem.style.display = "none";
                htmlElem.setAttribute('data-isopened', '0');
            }
        }
    }

    openSubMenuHBItem(index: number) {
        let menuID = `hb-item-${index}`;
        let bpSubmenu = document.getElementById(menuID);
        if (bpSubmenu) {
            let accIsOpened = bpSubmenu.getAttribute('data-isopened');
            if (accIsOpened && accIsOpened == '0') {
                let dropdownSubmenus = document.getElementsByClassName('hb-item-dropdown');
                for (let i = 0; i < dropdownSubmenus.length; i++) {
                    let htmlElem = <HTMLElement>dropdownSubmenus[i];
                    htmlElem.style.display = "none";
                    htmlElem.setAttribute('data-isopened', '0');
                }
                bpSubmenu.style.display = "block";
                bpSubmenu.setAttribute('data-isopened', '1');
                window.addEventListener('click', this.windowClickHBDropdownHandler);
            } else {
                bpSubmenu.style.display = "none";
                bpSubmenu.setAttribute('data-isopened', '0');
                window.removeEventListener('click', this.windowClickHBDropdownHandler);
            }
        }
        return false;
    }

    openEditHBForm(index: number) {
        this.edHeartBeat.id = this.heartBeatList[index].id;
        this.edHeartBeat.rate = this.heartBeatList[index].rate;
        this.edHeartBeat.note = this.heartBeatList[index].note;
        this.edHeartBeat.time = this.heartBeatList[index].time;

        // Change style
        for (let i = 0; i < this.heartBeatList.length; i++) {
            let editHBForm = document.getElementById(`ehb-form-${i}`);
            if (editHBForm) {
                editHBForm.style.display = 'none';
            }
        }
        for (let i = 0; i < this.heartBeatList.length; i++) {
            let hbRecord = document.getElementById(`hb-record-${i}`);
            if (hbRecord) {
                hbRecord.style.display = 'block';
            }
        }
        let editBSForm = document.getElementById(`ehb-form-${index}`);
        let bpRecord = document.getElementById(`hb-record-${index}`);
        if (editBSForm && bpRecord) {
            editBSForm.style.display = 'block';
            bpRecord.style.display = 'none';
        }

        return false;
    }

    deleteHBItem(index: number) {
        let result = confirm('Do you really want to delete this heartbeat record ? ');
        if (result) {
            this.heartBeatList.splice(index, 1);
        }
        return false;
    }
    //END BloodPressure
}


