import { Component, ViewEncapsulation, OnInit } from '@angular/core';
import {  ROUTER_DIRECTIVES, Router } from '@angular/router';
import { HTTP_PROVIDERS } from '@angular/http';

import { SigninComponent } from './account/signin.component';
import { SignupComponent } from './account/signup.component';
import { SignupPatientComponent } from './account/signup-patient.component';
import { SignupDoctorComponent } from './account/signup-doctor.component';
import { IntroComponent } from './home/intro.component';
import { IntroPatientComponent } from './home/intro-patient.component';
import { IntroDoctorComponent } from './home/intro-doctor.component';
import { ContactComponent } from './home/contact.component';
import { PrivacyPolicyComponent } from './home/privacypolicy.component';
import { TermOfServiceComponent } from './home/tos.component';
import { ForgotPwdComponent } from './account/forgotpwd.component';
import { PatientComponent } from './patient/patient.component';
import { DoctorComponent } from './doctor/doctor.component';

import { Account } from './account/account.model';

@Component({
    selector: 'app',
    template: require('./app.component.html'),
    styles: [
        require('../sass/main.scss')
    ],
    encapsulation: ViewEncapsulation.None,
    directives: [ROUTER_DIRECTIVES],
    providers: [
        HTTP_PROVIDERS
    ],
    precompile: [SigninComponent,
                SignupComponent,
                SignupPatientComponent,
                SignupDoctorComponent,
                IntroComponent,
                IntroPatientComponent,
                ContactComponent,
                PrivacyPolicyComponent,
                TermOfServiceComponent,
                ForgotPwdComponent,
                PatientComponent,
                DoctorComponent
                ]
})
export class AppComponent implements OnInit {
    private account: Account;

    constructor(private router: Router) { }
    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));
    }

    signout() {
        this.account = null;
        window.localStorage.setItem('account', null);
        this.router.navigate(['/signin']);
    }

    // Get current section that is presenting -> crap!
    currentSection() {
        let current = {};

        let pathArr = window.location.pathname.split('/');
        if (pathArr.length == 2 && pathArr[1] == 'signin') {
            current['signin'] = true;
        } else if (pathArr.length >= 2 && pathArr[1] == 'signup') {
            current['signup'] = true;
        } else if (pathArr.length == 2 && pathArr[1] == ''
            || window.location.pathname.includes('intro/patient')
            || window.location.pathname.includes('intro/doctor')) {
            current['signin'] = true;
            current['signup'] = true;
        } else if (pathArr.length >= 2 && pathArr[1] == 'patient') {
            current['patient'] = true;
        }
        return current;
    }

    getActiveSecondaryMenu() {
        let loc = window.location.pathname.split('/');
        if (loc.length >= 3 && loc[3] == 'appointments') {
            return 0;
        } else if (loc.length >= 3 && loc[3] == 'medicine') {
            return 1;
        } else if (loc.length >= 3 && loc[3] == 'medicalrecords') {
            return 2;
        } else if (loc.length == 3 && loc[1] == 'patients' || loc[1] == 'doctors') {
            return 3;
        } else if (loc.length >= 3 && loc[3] == 'stats') {
            return 4;
        }
    }

    // START Drodown
    windowClickDropdownHandler(event: any) {
        if (event.target.id != 'acc-dropdown'
            && event.target.id != 'noti-dd-trigger'
            && event.target.id != 'noti-dropdown'
            && event.target.id != 'acc-dd-trigger') {
            let accDropdownElem = document.getElementById('acc-dropdown');
            let notiDropdownElem = document.getElementById('noti-dropdown');
            if (accDropdownElem) {
                let isOpened = accDropdownElem.getAttribute('data-isopened');
                if (isOpened && isOpened == '1') {
                    accDropdownElem.style.display = 'none';
                    accDropdownElem.setAttribute('data-isopened', '0');
                    window.removeEventListener('click', this.windowClickDropdownHandler);
                }
            }
            if (notiDropdownElem) {
                let isOpened = notiDropdownElem.getAttribute('data-isopened');
                if (isOpened && isOpened == '1') {
                    notiDropdownElem.style.display = 'none';
                    notiDropdownElem.setAttribute('data-isopened', '0');
                    window.removeEventListener('click', this.windowClickDropdownHandler);
                }
            }
        }
    }

    openDropdownAccount() {
        let accDropdownElem = document.getElementById('acc-dropdown');
        if (accDropdownElem) {
            let accIsOpened = accDropdownElem.getAttribute('data-isopened');
            if (accIsOpened && accIsOpened == '0') {
                let notiDropdownElem = document.getElementById('noti-dropdown');
                notiDropdownElem.style.display = "none";
                notiDropdownElem.setAttribute('data-isopened', '0');
                accDropdownElem.style.display = "block";
                accDropdownElem.setAttribute('data-isopened', '1');
                window.addEventListener('click', this.windowClickDropdownHandler);
            } else {
                accDropdownElem.style.display = "none";
                accDropdownElem.setAttribute('data-isopened', '0');
                window.removeEventListener('click', this.windowClickDropdownHandler);
            }
        }
        return false;
    }

    openDropdownNoti() {
        let notiDropdownElem = document.getElementById('noti-dropdown');
        let isOpened = notiDropdownElem.getAttribute('data-isopened');
        if (isOpened && isOpened == '0') {
            notiDropdownElem.style.display = "block";
            notiDropdownElem.setAttribute('data-isopened', '1');
            let accDropdownElem = document.getElementById('acc-dropdown');
            accDropdownElem.style.display = "none";
            accDropdownElem.setAttribute('data-isopened', '0');
            window.addEventListener('click', this.windowClickDropdownHandler);
        } else {
            notiDropdownElem.style.display = "none";
            notiDropdownElem.setAttribute('data-isopened', '0');
            window.removeEventListener('click', this.windowClickDropdownHandler);
        }

        return false;
    }
}



