	module.exports = "<article id=\"prescription-details-container\" class=\"card\">\r\n    <header>\r\n        <h3>Prescription details</h3>\r\n        <div id=\"dp-edit-del-action\" *ngIf=\"prescription.creator.id == account.id\">\r\n            <button class=\"btn-normal\" id=\"btn-edit-prescription\" (click)=\"goToEditPrescription()\">Edit</button>\r\n            <button class=\"btn-normal\" id=\"btn-del-prescription\" (click)=\"deletePrescription()\">Delete</button>\r\n        </div>\r\n    </header>\r\n    <div id=\"dp-content\">\r\n        <div id=\"dp-meds-container\">\r\n            <h4 id=\"meds-header\" (click)=\"toggleMedicineContainer()\"><i class=\"fa fa-angle-up\" aria-hidden=\"true\"></i> Medicine - {{medicines.length}}</h4>\r\n            <div id=\"dp-meds-content\">\r\n                <span *ngIf=\"medicines.length == 0\">There is no medicine in this prescription</span>\r\n                <h5 *ngIf=\"medicines.length > 0\">List of medicines</h5>\r\n                <div id=\"meds-container\" *ngIf=\"medicines.length > 0\">\r\n                    <ul>\r\n                        <li *ngFor=\"let med of medicines; let i=index\" class=\"med-item\">\r\n                            <span class=\"med-order\">{{i+1}}.</span>\r\n                            <div class=\"med-content\">\r\n                                <span class=\"item-name\"><span>Name: </span>{{med.name}}</span>\r\n                                <span class=\"item-quantity\"><span>Quantity: </span>{{med.quantity}}</span>\r\n                                <span class=\"item-unit\"><span>Unit: </span>{{med.unit}}</span>\r\n                                <span class=\"item-note\"><span>Note: </span>{{med.note}}</span>\r\n                            </div>\r\n                        </li>\r\n                    </ul>\r\n                </div>\r\n            </div>\r\n        </div>\r\n\r\n        <div id=\"dp-imgatt-container\">\r\n            <h4 id=\"att-header\" (click)=\"toggleAttachmentContainer()\"><i class=\"fa fa-angle-down\" aria-hidden=\"true\"></i> Image Attachment - {{attachments.length}}</h4>\r\n            <div id=\"dp-imgatt-content\">\r\n                <div class=\"att-item\" *ngFor=\"let item of attachments; let i=index\">\r\n                    <a [attr.href]=\"item.file\" data-lightbox=\"att-image-set\" [attr.data-title]=\"item.description\"><img [attr.src]=\"item.file\" [attr.alt]=\"item.description\"></a>\r\n                </div>\r\n            </div>\r\n        </div>\r\n\r\n        <div id=\"dp-extinfo-container\">\r\n            <div id=\"pre-medrecord\">\r\n                <span class=\"item-exp\">Medical record</span>\r\n                <span class=\"item-content\"><a [routerLink]=\"['/patients/' + patientID + '/medrecords/' + prescription.medicalRecord.id]\">{{prescription.medicalRecord.id}}</a></span>\r\n            </div>\r\n            <span id=\"dp-from\"><span>From: </span>{{prescription.from}}</span>\r\n            <span id=\"dp-to\"><span>To: </span>{{prescription.to}}</span>\r\n            <span id=\"dp-note\"><span>Note: </span>{{prescription.note}}</span>\r\n        </div>\r\n    </div>\r\n</article>";

