import {Component, OnInit} from "@angular/core";
import {Router, ROUTER_DIRECTIVES} from "@angular/router";
import {NgForm} from "@angular/common";
import Logger from "../utils/logger.service";
import Country from "../utils/country";
import {AccountService} from "./account.service";
import {SignupPatientAccount, Account} from "./account.model";

@Component({
   selector: 'signup-patient',
   template: require('./signup-patient.component.html'),
   styles: [
      require('../../sass/pages/signup.patient.scss')
   ],  
   directives: [ROUTER_DIRECTIVES],
   providers: [AccountService]
})

export class SignupPatientComponent implements OnInit{
    private account: Account;
    private model: SignupPatientAccount;
    private logger: Logger;
    
    constructor(private router: Router,
                private accountService: AccountService) {
        this.logger = new Logger("SignupComponent");      
        this.model = new SignupPatientAccount();
    }
    
    ngOnInit() {

        // Check account already signed in
        this.account = JSON.parse(window.localStorage.getItem('account'));
        if (this.account && this.account.role == 0) {
            this.router.navigate([`/patients/${this.account.id}/appointments`]);
        } else if (this.account && this.account.role == 1) {
            this.router.navigate([`/patients/${this.account.id}/appointments`]);
        } 
    }
    
    signUp() {
        this.logger.log("On submitting...");
        // Request signup service

    }
    
    get diagnostic() {
        return JSON.stringify(this.model);
    }
} 



