import { provideRouter, RouterConfig } from '@angular/router';

import { PatientComponent } from "./patient.component";
import { ProfileComponent } from "./profile.component";
import { AppointsComponent } from "./patient.appoints.component";
import { AppointsTotalComponent } from "./patient.appoints.activated.component";
import { AppointsPendingComponent } from "./patient.appoints.pending.component";
import { AppointDetailsComponent } from "./patient.appoint.details.component";
import { AppointBrowserComponent } from "./patient.appoint.browser.component";
import { PrescriptionComponent } from "./patient.prescription.component";
import { AddPrescriptionComponent } from "./patient.prescription.add.component";
import { EditPrescriptionComponent } from "./patient.prescription.edit.component";
import { PrescriptionDetailComponent } from "./patient.prescription.detail.component";
import { MedRecordPatientComponent } from "./patient.medrecord.component";
import { MedRecordDetailsPatientComponent } from "./patient.medrecord.details.component";
import { MedIndexPatientComponent } from "./patient.medindex.component";
import { MedDiaryPatientComponent } from "./patient.meddiary.component";
import { ExpNoteDetailsComponent } from "./patient.expnote.details.component";
import { ExpNoteAddComponent } from "./patient.expnote.add.component";
import { StatsBPPatientComponent } from "./patient.stats.bp.component";
import { StatsBSPatientComponent } from "./patient.stats.bs.component";
import { StatsHBPatientComponent } from "./patient.stats.hb.component";

import { AuthGuard } from "../utils/auth.guard.ts";

export const patientRoutes: RouterConfig = [
    {
        path: 'patients',
        component: PatientComponent,
        canActivate: [AuthGuard],
        children: [
            {
                path: ':acid/appointments',
                component: AppointsComponent,
                children: [
                    { path: '', component: AppointsTotalComponent },
                    { path: 'pending', component: AppointsPendingComponent },
                    { path: 'browser', component: AppointBrowserComponent }
                ]
            },
            { 
                path: ':acid/appointments/:apid', 
                component: AppointDetailsComponent 
            },
            {
                path: ':acid',
                component: ProfileComponent
            },
            {
                path: ':acid/stats/bloodpressure',
                component: StatsBPPatientComponent
            },
            {
                path: ':acid/stats/bloodsugar',
                component: StatsBSPatientComponent
            },
            {
                path: ':acid/stats/heartbeat',
                component: StatsHBPatientComponent
            },
            {
                path: ':acid/medrecords',
                component: MedRecordPatientComponent
            },
            {
                path: ':acid/medrecords/:mrid',
                component: MedRecordDetailsPatientComponent
            },
            {
                path: ':acid/medindex',
                component: MedIndexPatientComponent
            },
            {
                path: ':acid/diary',
                component: MedDiaryPatientComponent
            },
            {
                path: ':acid/medrecords/:mrid/expnotes/add',
                component: ExpNoteAddComponent
            },
            {
                path: ':acid/medrecords/:mrid/expnotes/:enid',
                component: ExpNoteDetailsComponent
            },
            {
                path: ':acid/prescriptions',
                component: PrescriptionComponent
            },
            {
                path: ':acid/medrecords/:mrid/prescriptions/add',
                component: AddPrescriptionComponent
            },
            {
                path: ':acid/medrecords/:mrid/prescriptions/:pid/edit',
                component: EditPrescriptionComponent
            },
            {
                path: ':acid/medrecords/:mrid/prescriptions/:pid',
                component: PrescriptionDetailComponent
            },
        ]
    },

];



