import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute, ROUTER_DIRECTIVES } from "@angular/router";
import {Http, Response} from "@angular/http";

import { Account } from "../account/account.model";
import { Doctor } from "./doctor.model";
import { Patient, Comment } from "../patient/patient.model";

import { patientRepo } from "../utils/data/patients";
import { doctorRepo } from "../utils/data/doctors";
import { commentRepo } from "../utils/data/comments";

@Component({
    selector: 'doctor-profile',
    template: require("./profile.component.html"),
    styles: [
        require("../../sass/pages/doctor.profile.scss")
    ],
    directives: [ROUTER_DIRECTIVES]
})
export class ProfileComponent implements OnInit {
    private account: Account;
    private doctorID = '';
    
    private doctor: Doctor;
    private patients: Patient[] = [];
    private comments: Comment[] = [];

    private editingProfile: EditingProfile = new EditingProfile();

    private isAuthentic = false;

    constructor(private router: Router,
        private activatedRoute: ActivatedRoute) { }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));
        this.doctorID= this.activatedRoute.snapshot.params['acid'];
        if (this.doctorID== this.account.id) {
            this.isAuthentic = true;
        }

        this.doctor = doctorRepo.getDoctorByID(this.doctorID);
        this.comments = commentRepo.getAllComments();
        this.patients = patientRepo.getAll();

    }
    

    // START modal
    modelClickPrordoctorsHandler(event: any) {
        if (event.target.id == 'prodoctors-modal') {
            let modal = document.getElementById('prodoctors-modal');
            if (modal && modal.getAttribute('data-isopened') == '1') {
                modal.style.display = "none";
                modal.setAttribute('data-isopened', '0');
                modal.removeEventListener('click', this.modelClickPrordoctorsHandler);
            }
        }
    }

    openPrordoctorsModal() {
        let modal = document.getElementById('prodoctors-modal');
        if (modal && modal.getAttribute('data-isopened') == '0') {
            modal.style.display = "flex";
            modal.setAttribute('data-isopened', '1');
            modal.addEventListener('click', this.modelClickPrordoctorsHandler);
        }
        return false;
    }

    closeProdoctorsModal() {
        let modal = document.getElementById('prodoctors-modal');
        if (modal) {
            modal.style.display = "none";
            modal.setAttribute('data-isopened', '0');
            modal.removeEventListener('click', this.modelClickPrordoctorsHandler);
        }
        return false;
    }
    // END modal

    // START Edit profile info
    editProfileInfo() {

        // Close edit appointment container
        let editArticle = document.getElementById('edit-profile-info');
        let detailArticle = document.getElementById('acc-info-content');
        editArticle.style.display = "none";
        detailArticle.style.display = "block";
        let btnEditAppoint = document.getElementById('btn-edit-appoint-details');
        btnEditAppoint.style.opacity = '1';
        btnEditAppoint.style.pointerEvents = 'auto';
        return false;
    }

    openEditProfileInfo() {
        this.editingProfile.id = this.account.id;
        this.editingProfile.firstName = this.account.firstName;
        this.editingProfile.lastName = this.account.lastName;
        this.editingProfile.address = this.account.address;
        this.editingProfile.phone = this.account.phone;
        this.editingProfile.dob = this.account.dob;
        this.editingProfile.photo = this.account.photo;

        let editArticle = document.getElementById('edit-profile-info');
        let detailArticle = document.getElementById('acc-info-content');
        editArticle.style.display = "block";
        detailArticle.style.display = "none";
        let btnEditAppoint = document.getElementById('btn-edit-appoint-details');
        btnEditAppoint.style.opacity = '0.6';
        btnEditAppoint.style.pointerEvents = 'none';

        return false;
    }

    closeEditProfileInfo() {
        let editArticle = document.getElementById('edit-profile-info');
        let detailArticle = document.getElementById('acc-info-content');
        editArticle.style.display = "none";
        detailArticle.style.display = "block";
        let btnEditAppoint = document.getElementById('btn-edit-appoint-details');
        btnEditAppoint.style.opacity = '1';
        btnEditAppoint.style.pointerEvents = 'auto';
        return false;
    }
    // END edit profile info

    chooseProfilePhotoChange() {
        let reader = new FileReader();
        reader.onload = (e) => {
            this.editingProfile.photo = e.target['result'];
        }

        let filePhotoElem = document.getElementById('u_img_file');
        let filePhotoInput = <HTMLInputElement>filePhotoElem;
        if (filePhotoInput.files && filePhotoInput.files[0]) {
            reader.readAsDataURL(filePhotoInput.files[0]);
        }
    }

    goToPatients() {
        this.router.navigate([`/doctors/${this.account.id}/patients`]);
        return false;
    }
    
    diagnostic() {
       
    }
}

class EditingProfile {
    id: string;
    photo: string;
    firstName: string;
    lastName: string;
    address: string;
    phone: string;
    dob: string;
}


