import {Component, OnInit} from "@angular/core";
import {Router, ROUTER_DIRECTIVES} from "@angular/router";
import {NgForm} from "@angular/common";

import { AccountSettingsComponent } from "./settings.account.component";
import { EmailSettingsComponent } from "./settings.email.component";
import { NotiSettingsComponent } from "./settings.noti.component";
import { BillingSettingsComponent } from "./settings.billing.component";

import Logger from "../utils/logger.service";
import { Account} from "../account/account.model";

@Component({
   selector: 'settings',
   template: require('./settings.component.html'),
   styles: [
      require('../../sass/pages/settings.scss')
   ], 
   directives: [ROUTER_DIRECTIVES],
   providers: [],
   precompile: [AccountSettingsComponent,
                EmailSettingsComponent,
                NotiSettingsComponent,
                BillingSettingsComponent]
})

export class SettingsComponent implements OnInit{
    private account: Account;

    constructor() {}

    ngOnInit(){
        this.account = JSON.parse(window.localStorage.getItem('account'));
    }

    getActiveSidebarMenu() {
        let loc = window.location.pathname.split('/');
        if (loc.length == 3 && loc[2] == 'password') {
            return 0;
        } else if (loc.length == 3 && loc[2] == 'email') {
            return 1;
        } else if (loc.length == 3 && loc[2] == 'notification') {
            return 2;
        } else if (loc.length == 3 && loc[2] == 'billing') {
            return 3;
        }
    }
}


