import {Component, OnInit} from "@angular/core";
import {Router, ActivatedRoute, ROUTER_DIRECTIVES} from "@angular/router";
import {FormGroup, Validators, FormControl, REACTIVE_FORM_DIRECTIVES} from '@angular/forms';

import { Account} from "../account/account.model";
import { Prescription, Medicine, Attachment, MedicalRecord, MedicalRecordCategory } from "./patient.model";

import { prescriptionRepo, medicines, images } from "../utils/data/prescriptions";
import { medicalRecordRepo, medrecordCategories } from "../utils/data/medrecords";

@Component({
    selector: 'patient-prescription-edit',
    template: require('./patient.prescription.add.component.html'),
    styles: [
        require('../../sass/pages/prescription.add.scss')
    ],
    directives: [REACTIVE_FORM_DIRECTIVES, ROUTER_DIRECTIVES],
    providers: []
})

export class AddPrescriptionComponent implements OnInit {
    private account: Account;
    private cprescriptions: Prescription[] = [];
    private hprescriptions: Prescription[] = [];

    private prescriptionModel  = new Prescription();
    private medicines: Medicine[] = [];
    private addMedicineModel: Medicine = new Medicine();
    private imgAttachments: Attachment[] = [];
    private medicalRecordID = '';

    private medicalRecords: MedicalRecord[] = [];
    private medCategories: MedicalRecordCategory[] = [];

    constructor(private router: Router,
                private activatedRoute: ActivatedRoute) { }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));
        this.medicalRecordID = this.activatedRoute.snapshot.params['mrid'];

        this.medicalRecords = medicalRecordRepo.getAll();
        this.medCategories = medrecordCategories;
    }

    savePrescription() {

        return false;
    }

    addOrUpdateNewMedicine() {
        if (this.addMedicineModel.id) {
            for (let i = 0; i < this.medicines.length; i++) {
                if (this.medicines[i].id == this.addMedicineModel.id) {
                    this.medicines[i].name = this.addMedicineModel.name;
                    this.medicines[i].quantity = this.addMedicineModel.quantity;
                    this.medicines[i].unit = this.addMedicineModel.unit;
                    this.medicines[i].note = this.addMedicineModel.note;

                    this.addMedicineModel = new Medicine();
                    return false;
                }
            }
        } else {
            let med = new Medicine();
            med.id = this.medicines.length + '';
            med.name = this.addMedicineModel.name;
            med.quantity = this.addMedicineModel.quantity;
            med.unit = this.addMedicineModel.unit;
            med.note = this.addMedicineModel.note;
            this.medicines.push(med);
        }

        this.addMedicineModel = new Medicine();

        return false;
    }

    deleteMedicine(index: number) {
        this.medicines.splice(index, 1);
    }

    editMedicine(index: number) {
        if (this.addMedicineModel.name || this.addMedicineModel.quantity || this.addMedicineModel.unit || this.addMedicineModel.note) {
            let agree = confirm('You are adding new medicine. Do you still want to edit chosen item?');
            if (agree) {
                this.addMedicineModel.id = this.medicines[index].id;
                this.addMedicineModel.name = this.medicines[index].name;
                this.addMedicineModel.quantity = this.medicines[index].quantity;
                this.addMedicineModel.unit = this.medicines[index].unit;
                this.addMedicineModel.note = this.medicines[index].note;
            }

            return false;
        }

        this.addMedicineModel.id = this.medicines[index].id;
        this.addMedicineModel.name = this.medicines[index].name;
        this.addMedicineModel.quantity = this.medicines[index].quantity;
        this.addMedicineModel.unit = this.medicines[index].unit;
        this.addMedicineModel.note = this.medicines[index].note;

        return false;
    }

    chooseImageAttachement() {
        this.eventFire(document.getElementById('u_img_attachment'), 'click');
        return false;
    }
    chooseImageChange() {
        let reader = new FileReader();
        reader.onload = (e) => {
            this.imgAttachments.push(new Attachment(e.target['result']));
        }

        let filePhotoElem = document.getElementById('u_img_attachment');
        let filePhotoInput = <HTMLInputElement>filePhotoElem;
        if (filePhotoInput.files && filePhotoInput.files[0]) {
            reader.readAsDataURL(filePhotoInput.files[0]);
        }
    }
    deleteChosenImageAttachment(index: number) {
        this.imgAttachments.splice(index, 1);

        return false;
    }


    toggleMedicinesContent() {
        let medContent = document.getElementById('meds-content');
        if (medContent.style.display == 'none') {
            medContent.style.display = 'block';
            let medFormI = document.getElementById('med-header').firstElementChild;
            medFormI.className = 'fa fa-angle-up';
        } else {
            medContent.style.display = 'none';
            let medFormI = document.getElementById('med-header').firstElementChild;
            medFormI.className = 'fa fa-angle-down';
        }

        return false;
    }

    toggleImgAttachmentContent() {
        let attachmentList = document.getElementById('att-list');
        if (attachmentList.style.display == 'block') {
            attachmentList.style.display = 'none';
            let medFormI = document.getElementById('att-header').firstElementChild;
            medFormI.className = 'fa fa-angle-down';
        } else {
            attachmentList.style.display = 'block';
            let medFormI = document.getElementById('att-header').firstElementChild;
            medFormI.className = 'fa fa-angle-up';
        }
    }

    eventFire(el: any, etype: any) {
        if (el.fireEvent) {
            el.fireEvent('on' + etype);
        } else {
            let evObj = document.createEvent('Events');
            evObj.initEvent(etype, true, false);
            el.dispatchEvent(evObj);
        }
    }

    chooseMedicalRecord(item: MedicalRecord) {
        this.medicalRecordID = item.id;
        this.closeChangingMedicalRecordModal();
    }

    onChangeSelectMRCategory() {
        
    }

    // START modal
    clickMedicalRecordModalHandler(event: any) {
        if (event.target.id == 'medrecord-modal') {
            let modal = document.getElementById('medrecord-modal');
            if (modal && modal.getAttribute('data-isopened') == '1') {
                modal.style.display = "none";
                modal.setAttribute('data-isopened', '0');
                modal.removeEventListener('click', this.clickMedicalRecordModalHandler);
            }
        }
    }

    openChangingMedicalRecordModal() {
        let modal = document.getElementById('medrecord-modal');
        if (modal && modal.getAttribute('data-isopened') == '0') {
            modal.style.display = "flex";
            modal.setAttribute('data-isopened', '1');
            modal.addEventListener('click', this.clickMedicalRecordModalHandler);
        }
        return false;
    }

    closeChangingMedicalRecordModal() {
        let modal = document.getElementById('medrecord-modal');
        if (modal) {
            modal.style.display = "none";
            modal.setAttribute('data-isopened', '0');
            modal.removeEventListener('click', this.clickMedicalRecordModalHandler);
        }
        return false;
    }
    // END modal
}



