import { provideRouter, RouterConfig } from '@angular/router';

import { AccountSettingsComponent } from "./settings.account.component";
import { EmailSettingsComponent } from "./settings.email.component";
import { NotiSettingsComponent } from "./settings.noti.component";
import { BillingSettingsComponent } from "./settings.billing.component";
import { SettingsComponent } from "./settings.component";

export const settingsRoutes: RouterConfig = [
    {
        path: 'settings',
        component: SettingsComponent,
        children: [
            {
                path: 'password',
                component: AccountSettingsComponent,
            },
            {
                path: 'email',
                component: EmailSettingsComponent,
            },
            {
                path: 'notification',
                component: NotiSettingsComponent,
            },
            {
                path: 'billing',
                component: BillingSettingsComponent,
            }
        ]
    }
];



