import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute, ROUTER_DIRECTIVES } from "@angular/router";
import {Http, Response} from "@angular/http";

import { Account } from "../account/account.model";
import { Patient, Allergy, Addiction, Comment } from "./patient.model";
import { Doctor } from "../doctor/doctor.model";

import { patientRepo } from "../utils/data/patients";
import { doctorRepo } from "../utils/data/doctors";
import { allergyRepo } from "../utils/data/allergies";
import { addictionRepo } from "../utils/data/addictions";
import { commentRepo } from "../utils/data/comments";

@Component({
    selector: 'patient-profile',
    template: require("./profile.component.html"),
    styles: [
        require("../../sass/pages/patient.profile.scss")
    ],
    directives: [ROUTER_DIRECTIVES]
})
export class ProfileComponent implements OnInit {
    private account: Account;
    private patient: Patient;
    private allergies: Allergy[] = [];
    private addictions: Addiction[] = [];
    private comments: Comment[] = [];
    private doctors: Doctor[] = [];

    private editingProfile: EditingProfile = new EditingProfile();
    private allergyModel: Allergy = new Allergy();
    private edAllergyModel: Allergy = new Allergy();
    private addictionModel: Addiction = new Addiction();
    private edAddictionModel: Addiction = new Addiction();

    private isAuthentic = false;
    private patientID = '';

    constructor(private router: Router,
        private activatedRoute: ActivatedRoute) { }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));
        this.patientID= this.activatedRoute.snapshot.params['acid'];
        if (this.patientID== this.account.id) {
            this.isAuthentic = true;
        }

        this.patient = patientRepo.getPatientByID(this.patientID);
        this.allergies = allergyRepo.getAll();
        this.addictions = addictionRepo.getAll();
        this.comments = commentRepo.getAllComments();
        this.doctors = doctorRepo.getAll();
    }

    // START modal
    modelClickPrordoctorsHandler(event: any) {
        if (event.target.id == 'prodoctors-modal') {
            let modal = document.getElementById('prodoctors-modal');
            if (modal && modal.getAttribute('data-isopened') == '1') {
                modal.style.display = "none";
                modal.setAttribute('data-isopened', '0');
                modal.removeEventListener('click', this.modelClickPrordoctorsHandler);
            }
        }
    }

    openPrordoctorsModal() {
        let modal = document.getElementById('prodoctors-modal');
        if (modal && modal.getAttribute('data-isopened') == '0') {
            modal.style.display = "flex";
            modal.setAttribute('data-isopened', '1');
            modal.addEventListener('click', this.modelClickPrordoctorsHandler);
        }
        return false;
    }

    closeProdoctorsModal() {
        let modal = document.getElementById('prodoctors-modal');
        if (modal) {
            modal.style.display = "none";
            modal.setAttribute('data-isopened', '0');
            modal.removeEventListener('click', this.modelClickPrordoctorsHandler);
        }
        return false;
    }
    // END modal

    // START Edit profile info
    editProfileInfo() {

        // Close edit appointment container
        let editArticle = document.getElementById('edit-profile-info');
        let detailArticle = document.getElementById('acc-info-content');
        editArticle.style.display = "none";
        detailArticle.style.display = "block";
        let btnEditAppoint = document.getElementById('btn-edit-appoint-details');
        btnEditAppoint.style.opacity = '1';
        btnEditAppoint.style.pointerEvents = 'auto';
        return false;
    }

    openEditProfileInfo() {
        this.editingProfile.id = this.account.id;
        this.editingProfile.firstName = this.account.firstName;
        this.editingProfile.lastName = this.account.lastName;
        this.editingProfile.address = this.account.address;
        this.editingProfile.phone = this.account.phone;
        this.editingProfile.dob = this.account.dob;
        this.editingProfile.photo = this.account.photo;

        let editArticle = document.getElementById('edit-profile-info');
        let detailArticle = document.getElementById('acc-info-content');
        editArticle.style.display = "block";
        detailArticle.style.display = "none";
        let btnEditAppoint = document.getElementById('btn-edit-appoint-details');
        btnEditAppoint.style.opacity = '0.6';
        btnEditAppoint.style.pointerEvents = 'none';

        return false;
    }

    closeEditProfileInfo() {
        let editArticle = document.getElementById('edit-profile-info');
        let detailArticle = document.getElementById('acc-info-content');
        editArticle.style.display = "none";
        detailArticle.style.display = "block";
        let btnEditAppoint = document.getElementById('btn-edit-appoint-details');
        btnEditAppoint.style.opacity = '1';
        btnEditAppoint.style.pointerEvents = 'auto';
        return false;
    }
    // END edit profile info

    chooseProfilePhotoChange() {
        let reader = new FileReader();
        reader.onload = (e) => {
            this.editingProfile.photo = e.target['result'];
        }

        let filePhotoElem = document.getElementById('u_img_file');
        let filePhotoInput = <HTMLInputElement>filePhotoElem;
        if (filePhotoInput.files && filePhotoInput.files[0]) {
            reader.readAsDataURL(filePhotoInput.files[0]);
        }
    }

    // START Addiction
    saveNewAddiction() {

        // Close edit appointment container
        let addictionForm = document.getElementById('addiction-form');
        addictionForm.style.display = "none";
        let btnAdd = document.getElementById('btn-add-addiction');
        btnAdd.style.opacity = '1';
        btnAdd.style.pointerEvents = 'auto';
        return false;
    }

    openAddNewAddiction() {
        this.allergyModel = new Allergy();

        let addictionForm = document.getElementById('addiction-form');
        addictionForm.style.display = "block";
        let btnAdd = document.getElementById('btn-add-addiction');
        btnAdd.style.opacity = '0.6';
        btnAdd.style.pointerEvents = 'none';

        return false;
    }

    closeAddNewAddiction() {
        let addictionForm = document.getElementById('addiction-form');
        addictionForm.style.display = "none";
        let btnAdd = document.getElementById('btn-add-addiction');
        btnAdd.style.opacity = '1';
        btnAdd.style.pointerEvents = 'auto';
        return false;
    }
    openEditAddictionForm(index: number, item: Allergy) {
        this.edAddictionModel.id = item.id;
        this.edAddictionModel.cause = item.cause;
        this.edAddictionModel.note = item.note;

        // Change style
        for (let i=0; i < this.addictions.length; i++) {
            let editForm = document.getElementById(`eaddiction-form-${i}`);
            if (editForm) {
                editForm.style.display = 'none';
            }
        }
        for (let i=0; i < this.addictions.length; i++) {
            let contentElem = document.getElementById(`eaddiction-content-${i}`);
            if (contentElem) {
                contentElem.style.display = 'block';
            }
        }
        let formID = `eaddiction-form-${index}`;
        let editForm = document.getElementById(formID);
        editForm.style.display = 'block';
        let contentID = `eaddiction-content-${index}`;
        let contentElem = document.getElementById(contentID)
        contentElem.style.display = 'none';
        let dropdownSubmenus = document.getElementsByClassName('addiction-item-dropdown');
        for (let i = 0; i < dropdownSubmenus.length; i++) {
            let htmlElem = <HTMLElement>dropdownSubmenus[i];
            htmlElem.style.display = "none";
            htmlElem.setAttribute('data-isopened', '0');
        }

        return false;
    }

    closeEditAddictionForm(index: number) {
        let formID = `eaddiction-form-${index}`;
        let editForm = document.getElementById(formID);
        editForm.style.display = 'none';
        let contentID = `eaddiction-content-${index}`;
        let contentElem = document.getElementById(contentID)
        contentElem.style.display = 'block';

        return false;
    }

    deleteAddiction() {
        let dropdownSubmenus = document.getElementsByClassName('addiction-item-dropdown');
        for (let i = 0; i < dropdownSubmenus.length; i++) {
            let htmlElem = <HTMLElement>dropdownSubmenus[i];
            htmlElem.style.display = "none";
            htmlElem.setAttribute('data-isopened', '0');
        }

        return false;
    }

    saveEditAddiction(index: number) {
        let formID = `eaddiction-form-${index}`;
        let editForm = document.getElementById(formID);
        editForm.style.display = 'none';
        let contentID = `eaddiction-content-${index}`;
        let contentElem = document.getElementById(contentID)
        contentElem.style.display = 'block';

        return false;
    }

    windowClickAddictionDropdownHandler(event: any) {
        let targetElem = <HTMLElement>event.target;
        if (!targetElem.classList.contains('dropdown-item')
            && !targetElem.classList.contains('btn-submenu-i')) {
            let dropdownSubmenus = document.getElementsByClassName('addiction-item-dropdown');
            for (let i = 0; i < dropdownSubmenus.length; i++) {
                let htmlElem = <HTMLElement>dropdownSubmenus[i];
                htmlElem.style.display = "none";
                htmlElem.setAttribute('data-isopened', '0');
            }
        }
    }

    openSubMenuAddictionItem(index: number) {
        let menuID = `addiction-item-${index}`;
        let allergySubmenu = document.getElementById(menuID);
        if (allergySubmenu) {
            let accIsOpened = allergySubmenu.getAttribute('data-isopened');
            if (accIsOpened && accIsOpened == '0') {
                let dropdownSubmenus = document.getElementsByClassName('addiction-item-dropdown');
                for (let i = 0; i < dropdownSubmenus.length; i++) {
                    let htmlElem = <HTMLElement>dropdownSubmenus[i];
                    htmlElem.style.display = "none";
                    htmlElem.setAttribute('data-isopened', '0');
                }
                allergySubmenu.style.display = "block";
                allergySubmenu.setAttribute('data-isopened', '1');
                window.addEventListener('click', this.windowClickAddictionDropdownHandler);
            } else {
                allergySubmenu.style.display = "none";
                allergySubmenu.setAttribute('data-isopened', '0');
                window.removeEventListener('click', this.windowClickAddictionDropdownHandler);
            }
        }
        return false;
    }
    // END Addiction

    // START Allergy
    saveNewAllergy() {

        // Close edit appointment container
        let allergyForm = document.getElementById('allergy-form');
        allergyForm.style.display = "none";
        let btnEditAppoint = document.getElementById('btn-add-allergy');
        btnEditAppoint.style.opacity = '1';
        btnEditAppoint.style.pointerEvents = 'auto';
        return false;
    }

    openAddNewAllergy() {
        this.allergyModel = new Allergy();

        let allergyForm = document.getElementById('allergy-form');
        allergyForm.style.display = "block";
        let btnEditAppoint = document.getElementById('btn-add-allergy');
        btnEditAppoint.style.opacity = '0.6';
        btnEditAppoint.style.pointerEvents = 'none';

        return false;
    }

    closeAddNewAllergy() {
        let allergyForm = document.getElementById('allergy-form');
        allergyForm.style.display = "none";
        let btnEditAppoint = document.getElementById('btn-add-allergy');
        btnEditAppoint.style.opacity = '1';
        btnEditAppoint.style.pointerEvents = 'auto';
        return false;
    }
    openEditAllergyForm(index: number, item: Allergy) {
        this.edAllergyModel.id = item.id;
        this.edAllergyModel.cause = item.cause;
        this.edAllergyModel.note = item.note;

        // Change style
        for (let i=0; i < this.allergies.length; i++) {
            let editForm = document.getElementById(`eallergy-form-${i}`);
            if (editForm) {
                editForm.style.display = 'none';
            }
        }
        for (let i=0; i < this.allergies.length; i++) {
            let contentElem = document.getElementById(`eallergy-content-${i}`);
            if (contentElem) {
                contentElem.style.display = 'block';
            }
        }
        let formID = `eallergy-form-${index}`;
        let editForm = document.getElementById(formID);
        editForm.style.display = 'block';
        let contentID = `eallergy-content-${index}`;
        let contentElem = document.getElementById(contentID)
        contentElem.style.display = 'none';
        let dropdownSubmenus = document.getElementsByClassName('allergy-item-dropdown');
        for (let i = 0; i < dropdownSubmenus.length; i++) {
            let htmlElem = <HTMLElement>dropdownSubmenus[i];
            htmlElem.style.display = "none";
            htmlElem.setAttribute('data-isopened', '0');
        }

        return false;
    }

    closeEditAllergyForm(index: number) {
        let formID = `eallergy-form-${index}`;
        let editForm = document.getElementById(formID);
        editForm.style.display = 'none';
        let contentID = `eallergy-content-${index}`;
        let contentElem = document.getElementById(contentID)
        contentElem.style.display = 'block';

        return false;
    }

    deleteAllergy() {
        let dropdownSubmenus = document.getElementsByClassName('allergy-item-dropdown');
        for (let i = 0; i < dropdownSubmenus.length; i++) {
            let htmlElem = <HTMLElement>dropdownSubmenus[i];
            htmlElem.style.display = "none";
            htmlElem.setAttribute('data-isopened', '0');
        }

        return false;
    }

    saveEditAllergy(index: number) {
        let formID = `eallergy-form-${index}`;
        let editForm = document.getElementById(formID);
        editForm.style.display = 'none';
        let contentID = `eallergy-content-${index}`;
        let contentElem = document.getElementById(contentID)
        contentElem.style.display = 'block';

        return false;
    }

    windowClickDropdownHandler(event: any) {
        let targetElem = <HTMLElement>event.target;
        if (!targetElem.classList.contains('dropdown-item')
            && !targetElem.classList.contains('btn-submenu-i')) {
            let dropdownSubmenus = document.getElementsByClassName('allergy-item-dropdown');
            for (let i = 0; i < dropdownSubmenus.length; i++) {
                let htmlElem = <HTMLElement>dropdownSubmenus[i];
                htmlElem.style.display = "none";
                htmlElem.setAttribute('data-isopened', '0');
            }
        }
    }

    openSubMenuAllergyItem(index: number) {
        let menuID = `allergy-item-${index}`;
        let allergySubmenu = document.getElementById(menuID);
        if (allergySubmenu) {
            let accIsOpened = allergySubmenu.getAttribute('data-isopened');
            if (accIsOpened && accIsOpened == '0') {
                let dropdownSubmenus = document.getElementsByClassName('allergy-item-dropdown');
                for (let i = 0; i < dropdownSubmenus.length; i++) {
                    let htmlElem = <HTMLElement>dropdownSubmenus[i];
                    htmlElem.style.display = "none";
                    htmlElem.setAttribute('data-isopened', '0');
                }
                allergySubmenu.style.display = "block";
                allergySubmenu.setAttribute('data-isopened', '1');
                window.addEventListener('click', this.windowClickDropdownHandler);
            } else {
                allergySubmenu.style.display = "none";
                allergySubmenu.setAttribute('data-isopened', '0');
                window.removeEventListener('click', this.windowClickDropdownHandler);
            }
        }
        return false;
    }
    // END Allergy

    goToMedicalRecord() {
        this.router.navigate([`/patients/${this.patientID}/medrecords`]);
        return false;
    }
    goToDiary() {
        this.router.navigate([`/patients/${this.patientID}/diary`]);
        return false;
    }
    
    diagnostic() {
        return JSON.stringify(JSON.stringify(this.allergies[1]));
    }
}

class EditingProfile {
    id: string;
    photo: string;
    firstName: string;
    lastName: string;
    address: string;
    phone: string;
    dob: string;
}


