import { Injectable } from "@angular/core";
import { Component } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import * as moment from "moment";

import { Account } from "../account/account.model";
import { AppointmentOutput } from "./patient.model";
import { SingleOutputModel, ArrayOutputModel } from '../utils/output.model';

import { ISO8601DateFormat, ISO8601DateTimeFormat} from "../utils/constants";

@Injectable()
export class PatientAppointmentsServive {
    private URL_PATIENT_APPOINTS = 'http://127.0.0.1:8080/';

    constructor(private http: Http) { }

    getAllApoints(account: Account) {
        let headers = new Headers({
            'Email': account.email,
            'Password': account.password,
            'Accept-Language': 'en-US',
            'Content-Type': 'application/json'
        });
        let options = new RequestOptions({ headers: headers });
        return this.http.get(this.URL_PATIENT_APPOINTS , options)
            .map(this.extractAllApointsData)
            .catch(this.handlerError);
    }

    private extractAllApointsData(res: Response) {
        if (res.status == 200) {
            let data = res.json().Appointments;

            let appoints: AppointmentOutput[] = [];
            for (let item of data) {
                let appoint: AppointmentOutput = new AppointmentOutput();
                appoint.id  = item.Id;
                appoint.partner.id = item.Partner.Id;
                appoint.partner.firstName = item.Partner.FirstName;
                appoint.partner.lastName = item.Partner.LastName;
                appoint.note = item.Note;
                appoint.status = item.Status;
                if (item.From)
                    appoint.start = moment(item.From, ISO8601DateTimeFormat).format(ISO8601DateTimeFormat);
                if (item.To)
                    appoint.end = moment(item.From, ISO8601DateTimeFormat).format(ISO8601DateTimeFormat);
                appoints.push(appoint);
            }

            let arrOutput: ArrayOutputModel = new ArrayOutputModel(1, appoints, null);
            return arrOutput;
        } else {

        } 
    }

    private handlerError(error: any) {
        let errMsg: string;
        if (error.message) {
            errMsg = error.message;
        } else if (error.status) {
            errMsg = `${error.status} - ${error.statusText}`;
        } else {
            errMsg = 'Server error';
        }

        console.log(errMsg);
        return Observable.throw(errMsg);
    }
}


