import { provideRouter, RouterConfig } from '@angular/router';

import { patientRoutes } from "./patient/patient.routers";
import { doctorRoutes } from "./doctor/doctor.routers";
import { searchRoutes } from "./search/search.routers";
import { settingsRoutes } from "./settings/settings.routers";

import { AppComponent } from "./app.component";
import { SigninComponent } from './account/signin.component';
import { SignupComponent } from './account/signup.component';
import { SignupPatientComponent } from './account/signup-patient.component';
import { SignupDoctorComponent } from './account/signup-doctor.component';
import { IntroComponent } from './home/intro.component';
import { IntroPatientComponent } from './home/intro-patient.component';
import { IntroDoctorComponent } from './home/intro-doctor.component';
import { ContactComponent } from './home/contact.component';
import { PrivacyPolicyComponent } from './home/privacypolicy.component';
import { TermOfServiceComponent } from './home/tos.component';
import { ForgotPwdComponent } from './account/forgotpwd.component';
import { PatientComponent } from './patient/patient.component';

import { AuthGuard } from "./utils/auth.guard.ts";

export const routes: RouterConfig = [
    { path: '', component: IntroComponent},
    { path: 'intro/patient', component: IntroPatientComponent },
    { path: 'intro/doctor',  component: IntroDoctorComponent },
    { path: 'signin', component: SigninComponent },
    { path: 'signup', component: SignupComponent },
    { path: 'signup/patient', component: SignupPatientComponent },
    { path: 'signup/doctor', component: SignupDoctorComponent },
    { path: 'forgotpwd', component: ForgotPwdComponent },
    { path: 'contact', component: ContactComponent },
    { path: 'privacypolicy', component: PrivacyPolicyComponent },
    { path: 'termofservice', component: TermOfServiceComponent },
     ...patientRoutes,
     ...doctorRoutes,
     ...searchRoutes,
     ...settingsRoutes
];

export const APP_ROUTER_PROVIDERS = [
    provideRouter(routes),
    [AuthGuard]
];


