import {MedicalDiary} from "../../patient/patient.model";
import { ISO8601DateTimeFormat, ISO8601DateFormat } from "../constants";
import { doctorRepo } from "./doctors";
import { patientRepo} from "./patients";

class MedicalDiaryRepo {
    private diaries: MedicalDiary[] = [];

    constructor() {
        let pre = new MedicalDiary();
        pre.id = '1231e324rc';
        pre.owner = doctorRepo.getAll()[0];
        pre.content = 'Dolor repudiandae at mel, timeam mentitum partiendo ea vim. Usu cu case verterem evertitur, id prima hendrerit incorrupte mel. Has ut soluta petentium referrentur, mutat ipsum vocent te pri, mea probo definiebas scripserit et';
        pre.date = moment().format(ISO8601DateTimeFormat);
        pre.created = moment().format(ISO8601DateTimeFormat);

        let pre2 = new MedicalDiary();
        pre2.id = '1231e324rf';
        pre2.owner = doctorRepo.getAll()[0];
        pre2.content = 'Dolor repudiandae at mel, timeam mentitum partiendo ea vim. Usu cu case verterem evertitur, id prima hendrerit incorrupte mel. Has ut soluta petentium referrentur, mutat ipsum vocent te pri, mea probo definiebas scripserit et';
        pre2.date = moment().format(ISO8601DateTimeFormat);
        pre2.created = moment().format(ISO8601DateTimeFormat);
        
        let pre3 = new MedicalDiary();
        pre3.id = '1231e324rv';
        pre3.owner = doctorRepo.getAll()[0];
        pre3.content = 'Dolor repudiandae at mel, timeam mentitum partiendo ea vim. Usu cu case verterem evertitur, id prima hendrerit incorrupte mel. Has ut soluta petentium referrentur, mutat ipsum vocent te pri, mea probo definiebas scripserit et';
        pre3.date = moment().format(ISO8601DateTimeFormat);
        pre3.created = moment().format(ISO8601DateTimeFormat);
        
        let pre4 = new MedicalDiary();
        pre4.id = '1231e324rz';
        pre4.owner = doctorRepo.getAll()[0];
        pre4.content = 'Dolor repudiandae at mel, timeam mentitum partiendo ea vim. Usu cu case verterem evertitur, id prima hendrerit incorrupte mel. Has ut soluta petentium referrentur, mutat ipsum vocent te pri, mea probo definiebas scripserit et';
        pre4.date = moment().format(ISO8601DateTimeFormat);
        pre4.created = moment().format(ISO8601DateTimeFormat);

        this.diaries.push(pre);
        this.diaries.push(pre2);
        this.diaries.push(pre3);
        this.diaries.push(pre4);
    }

    getAll() {
        return this.diaries;
    }

    getPrescriptionByID(id: string) {
        for (let item of this.diaries) {
            if (item.id == id) 
                return item;
        }
        return null;
    }

    removePrescriptionByID(id: string) {
        let temp: MedicalDiary[] = [];
        for (let item of this.diaries) {
            if (item.id != id) 
                temp.push(item);
        }
        this.diaries = temp;
        return temp;
    }
}

export let diaryRepo = new MedicalDiaryRepo();


