import {Component, OnInit} from "@angular/core";
import {NgForm} from "@angular/common";
import {Router, ROUTER_DIRECTIVES} from "@angular/router-deprecated";
import Logger from "../utils/logger.service";
import {SigninAccount} from "./account.model";
import {AccountService} from "./account.service";

@Component({
   selector: 'signin',
   template: require('./forgotpwd.component.html'),
   styles: [
      require('../../sass/pages/forgotpwd.scss')
   ], 
   providers: [AccountService],
   directives: [ROUTER_DIRECTIVES]
})

export class ForgotPwdComponent implements OnInit{
    private isInvalid: boolean;
    private logger: Logger;
    private email: string;
    
    constructor(private accountService: AccountService) {
        this.logger = new Logger("SigninComponent");
    }
    
    ngOnInit() {
        this.isInvalid = false;
        this.email = '';
    }
    
    sendChangingPassword() {
        this.logger.log("On submiting....");
        // Request chagning password to this email service

        
        
    }
    
    get diagnostic() {
        return JSON.stringify(this.email);
    }
    
} 



