import {Component, OnInit} from "@angular/core";
import { Router } from "@angular/router";

import { Account } from "../account/account.model";

@Component({
    selector: 'intro',
    template: require("./intro.component.html"),
    styles: [
        require("../../sass/pages/intro.scss")
    ]
})
export class IntroComponent implements OnInit {
    private account: Account;

    constructor(private router: Router) {

    }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));

        if (this.account && this.account.role == 0) {
            this.router.navigate([`/patients/${this.account.id}/appointments`,]);
        } else if (this.account && this.account.role == 1){
            this.router.navigate([`/doctors/${this.account.id}/appointments`,]);
        }
    }
}


