import { Component, OnInit} from "@angular/core";
import { Router, ROUTER_DIRECTIVES } from '@angular/router';
import { Http, Response} from "@angular/http";
import { AppointmentOutput } from "../patient/patient.model";
import { DoctorAppointmentsServive } from "./doctor.appoints.service";
import { Account } from "../account/account.model";

import { AppointOutRepo } from "../utils/data/appoints";

@Component({
    selector: 'doctor-appoint-pending',
    template: require("./doctor.appoints.pending.component.html"),
    styles: [
        require("../../sass/pages/appoint.pending.scss"),
    ],
    directives: [ROUTER_DIRECTIVES],
    providers: [DoctorAppointmentsServive]
})
export class AppointsPendingComponent implements OnInit {
    private account: Account;
    private pendingAppoints: AppointmentOutput[] = [];

    private minePendingCBValue = true;
    private theirPendingCBValue = false;

    constructor(private appointService: DoctorAppointmentsServive) {

    }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));

        // get all appointments
        // this.appointService.getAllApoints(this.account)
        //     .subscribe(
        //     success => {
        //         if (success.status == 1) {
        //             this.pendingAppoints = success.data;
        //         } else {

        //         }
        //     },
        //     error => {

        //     }
        //     );
        // fake data
        let aRepo: AppointOutRepo = new AppointOutRepo();
        this.pendingAppoints = aRepo.getAllAppoints();

        // set active tab
        let liActivatedTabElem = document.getElementById('activated-appoints-tablink').parentElement.parentElement;
        let liPendingTabElem = document.getElementById('pending-appoints-tablink').parentElement.parentElement;
        liPendingTabElem.className += ' active';
        liActivatedTabElem.className = liPendingTabElem.className.replace(' active', '');
    }

    onPendingTypeCheckboxChange() {
        setTimeout(() => {
            
        }, 100);
        return false;
    }
    diagnostic() {
        return JSON.stringify(this.pendingAppoints);
    }
}


