import { Injectable } from "@angular/core";
import { Component } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { SingleOutputModel, ArrayOutputModel } from '../utils/output.model';
import { Account } from "../account/account.model";
import { Doctor } from "../doctor/doctor.model";

import { doctorRepo } from "../utils/data/doctors";

@Injectable()
export class SearchService {
    private URL_SEARCH_PROFILE_DOCTORS = 'http://127.0.0.1:8080/';

    constructor(private http: Http) { }

    searchDoctor(account: Account, name: string, specialtyId: string, countryId: string, cityId: string) {
        let headers = new Headers({
            'Email': account.email,
            'Password': account.password,
            'Accept-Language': 'en-US',
            'Content-Type': 'application/json'
        });
        let options = new RequestOptions({ headers: headers });
        return this.http.get(this.URL_SEARCH_PROFILE_DOCTORS , options)
            .map(this.extractAllApointsData)
            .catch(this.handlerError);
    }

    private extractAllApointsData(res: Response) {
        if (res.status == 200) {
            let data = res.json().Appointments;

            let doctors: Doctor[] = [];
            for (let item of data) {
                let doctor: Doctor = new Doctor();
                doctor.id  = item.Id;
                doctor.firstName = item.FirstName;
                doctor.lastName = item.LastName;
                doctor.speciality = item.Speciality;
                doctor.address = item.Address;
                doctor.rank = item.Rank;

                doctors.push(doctor);
            }

            let arrOutput: ArrayOutputModel = new ArrayOutputModel(1, doctors, null);
            //arrOutput.total = doctors.Total;
            return arrOutput;
        } else {

        } 
    }

    private handlerError(error: any) {
        let errMsg: string;
        if (error.message) {
            errMsg = error.message;
        } else if (error.status) {
            errMsg = `${error.status} - ${error.statusText}`;
        } else {
            errMsg = 'Server error';
        }

        console.log(errMsg);
        return Observable.throw(errMsg);
    }

}


