import { provideRouter, RouterConfig } from '@angular/router';


import { DoctorComponent } from "./doctor.component";
import { AppointsComponent } from "./doctor.appoints.component";
import { ProfileComponent } from "./profile.component";
import { AppointDetailsComponent } from "./doctor.appoint.details.component";
import { AppointsTotalComponent } from "./doctor.appoints.activated.component";
import { AppointsPendingComponent } from "./doctor.appoints.pending.component";
import { ConnPatientComponent } from "./connpatient.component";

import { AuthGuard } from "../utils/auth.guard.ts";

export const doctorRoutes: RouterConfig = [
    {
        path: 'doctors',
        component: DoctorComponent,
        canActivate: [AuthGuard],
        children: [
            {
                path: ':acid/appointments',
                component: AppointsComponent,
                children: [
                    { path: '', component: AppointsTotalComponent },
                    { path: 'pending', component: AppointsPendingComponent },
                ]
            },
            { 
                path: ':acid/appointments/:apid', 
                component: AppointDetailsComponent 
            },
            {
                path: ':acid',
                component: ProfileComponent
            },
            {
                path: ':acid/patients',
                component: ConnPatientComponent
            },
        ]
            
    },

];



