import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute, ROUTER_DIRECTIVES } from "@angular/router";
import {Http, Response} from "@angular/http";


import { Account } from "../account/account.model";
import { Patient, Allergy, Addiction, BloodPressure, HeartBeat, BloodSugar } from "./patient.model";
import { Doctor } from "../doctor/doctor.model";

import { patientRepo } from "../utils/data/patients";
import { bloodPressureRepo } from "../utils/data/bloodpressure";
import { heartBeatRepo } from "../utils/data/heartbeat";
import { sugarBloodRepo } from "../utils/data/sugarblood";

@Component({
    selector: 'patient-stats-bp',
    template: require("./patient.stats.bs.component.html"),
    styles: [
        require("../../sass/pages/stats.bs.scss")
    ],
    directives: [ROUTER_DIRECTIVES],
})
export class StatsBSPatientComponent implements OnInit {
    private account: Account;
    private patient: Patient;

    private bloodSugarList: BloodSugar[] = [];
    private bloodSugar: BloodSugar = new BloodSugar();
    private edBloodSugar: BloodSugar = new BloodSugar();

    constructor(private router: Router,
        private activatedRoute: ActivatedRoute) {
    }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));

        // Data
        this.bloodSugarList = sugarBloodRepo.getAll();

        this.drawBloodSugarChart();
    }

    drawBloodSugarChart() {
        let chartContainer = document.getElementById('bp-chart');
        var data = [{
            "sale": "202",
            "year": "2000"
        }, {
                "sale": "215",
                "year": "2001"
            }, {
                "sale": "179",
                "year": "2002"
            }, {
                "sale": "199",
                "year": "2003"
            }, {
                "sale": "134",
                "year": "2003"
            }, {
                "sale": "176",
                "year": "2010"
            }];
        // let visual = d3.select("#visualisation")    

    }
    // START BloodPressure
    toggleBloodSugarContent() {
        let bpVisual = document.getElementById('bs-visual');
        let bpHistory = document.getElementById('bs-history');
        if (bpVisual.style.display == 'block') {
            bpVisual.style.display = 'none';
            bpHistory.style.display = 'none';

            let bpAngle = document.getElementById('bs-angle');
            let iElem = <HTMLElement>document.createElement('i');
            iElem.className += 'fa fa-angle-double-down';
            iElem.setAttribute('aria-hidden', 'true');
            bpAngle.innerHTML = '';
            bpAngle.appendChild(iElem);

            // Close bp history
            let bpHistoryContent = document.getElementById('bs-history-content');
            if (bpHistoryContent.style.display == 'block') {
                bpHistoryContent.style.display = 'none';

                let bpAngle = document.getElementById('bs-content-angle');
                let iElem = <HTMLElement>document.createElement('i');
                iElem.className += 'fa fa-angle-down';
                iElem.setAttribute('aria-hidden', 'true');
                bpAngle.innerHTML = '';
                bpAngle.appendChild(iElem);
            }
        } else {
            bpVisual.style.display = 'block';
            bpHistory.style.display = 'block';

            let bpAngle = document.getElementById('bs-angle');
            let iElem = <HTMLElement>document.createElement('i');
            iElem.className += 'fa fa-angle-double-up';
            iElem.setAttribute('aria-hidden', 'true');
            bpAngle.innerHTML = '';
            bpAngle.appendChild(iElem);
        }
    }

    toggleBSHistoryContent() {
        let bpHistoryContent = document.getElementById('bs-history-content');
        if (bpHistoryContent.style.display == 'block') {
            bpHistoryContent.style.display = 'none';

            let bpAngle = document.getElementById('bs-content-angle');
            let iElem = <HTMLElement>document.createElement('i');
            iElem.className += 'fa fa-angle-down';
            iElem.setAttribute('aria-hidden', 'true');
            bpAngle.innerHTML = '';
            bpAngle.appendChild(iElem);
        } else {
            bpHistoryContent.style.display = 'block';

            let bpAngle = document.getElementById('bs-content-angle');
            let iElem = <HTMLElement>document.createElement('i');
            iElem.className += 'fa fa-angle-up';
            iElem.setAttribute('aria-hidden', 'true');
            bpAngle.innerHTML = '';
            bpAngle.appendChild(iElem);
        }
    }

    saveNewBloodSugar() {

        // Close edit appointment container
        let bpForm = document.getElementById('bs-form');
        bpForm.style.display = "none";
        let btnAdd = document.getElementById('btn-add-bp');
        btnAdd.style.opacity = '1';
        btnAdd.style.pointerEvents = 'auto';
        return false;
    }

    openAddNewBloodSugar() {
        this.bloodSugar = new BloodSugar();

        let bpForm = document.getElementById('bs-form');
        bpForm.style.display = "block";
        let btnAdd = document.getElementById('btn-add-bs');
        btnAdd.style.opacity = '0.6';
        btnAdd.style.pointerEvents = 'none';

        return false;
    }

    closeAddNewBloodSugar() {
        let bpForm = document.getElementById('bs-form');
        bpForm.style.display = "none";
        let btnAdd = document.getElementById('btn-add-bs');
        btnAdd.style.opacity = '1';
        btnAdd.style.pointerEvents = 'auto';
        return false;
    }

    closeEditBloodSugarForm(index: number) {
        let editBSForm = document.getElementById(`ebs-form-${index}`);
        let bsRecord = document.getElementById(`bs-record-${index}`);
        if (editBSForm && bsRecord) {
            editBSForm.style.display = 'none';
            bsRecord.style.display = 'block';
        }

        return false;
    }
    saveEditBloodSugar(index: number) {
        // Save data

        // Update style
        this.closeEditBloodSugarForm(index);

        return false;
    }

    windowClickBSDropdownHandler(event: any) {
        let targetElem = <HTMLElement>event.target;
        if (!targetElem.classList.contains('dropdown-item')
            && !targetElem.classList.contains('btn-submenu-i')) {
            let dropdownSubmenus = document.getElementsByClassName('bs-item-dropdown');
            for (let i = 0; i < dropdownSubmenus.length; i++) {
                let htmlElem = <HTMLElement>dropdownSubmenus[i];
                htmlElem.style.display = "none";
                htmlElem.setAttribute('data-isopened', '0');
            }
        }
    }

    openSubMenuBSItem(index: number) {
        let menuID = `bs-item-${index}`;
        let bpSubmenu = document.getElementById(menuID);
        if (bpSubmenu) {
            let accIsOpened = bpSubmenu.getAttribute('data-isopened');
            if (accIsOpened && accIsOpened == '0') {
                let dropdownSubmenus = document.getElementsByClassName('bs-item-dropdown');
                for (let i = 0; i < dropdownSubmenus.length; i++) {
                    let htmlElem = <HTMLElement>dropdownSubmenus[i];
                    htmlElem.style.display = "none";
                    htmlElem.setAttribute('data-isopened', '0');
                }
                bpSubmenu.style.display = "block";
                bpSubmenu.setAttribute('data-isopened', '1');
                window.addEventListener('click', this.windowClickBSDropdownHandler);
            } else {
                bpSubmenu.style.display = "none";
                bpSubmenu.setAttribute('data-isopened', '0');
                window.removeEventListener('click', this.windowClickBSDropdownHandler);
            }
        }
        return false;
    }

    openEditBSForm(index: number) {
        this.edBloodSugar.id = this.bloodSugarList[index].id;
        this.edBloodSugar.value = this.bloodSugarList[index].value;
        this.edBloodSugar.note = this.bloodSugarList[index].note;
        this.edBloodSugar.time = this.bloodSugarList[index].time;

        // Change style
        for (let i = 0; i < this.bloodSugarList.length; i++) {
            let editBSForm = document.getElementById(`ebs-form-${i}`);
            if (editBSForm) {
                editBSForm.style.display = 'none';
            }
        }
        for (let i = 0; i < this.bloodSugarList.length; i++) {
            let bsRecord = document.getElementById(`bs-record-${i}`);
            if (bsRecord) {
                bsRecord.style.display = 'block';
            }
        }
        let editBSForm = document.getElementById(`ebs-form-${index}`);
        let bpRecord = document.getElementById(`bs-record-${index}`);
        if (editBSForm && bpRecord) {
            editBSForm.style.display = 'block';
            bpRecord.style.display = 'none';
        }

        return false;
    }

    deleteBPItem(index: number) {
        let result = confirm('Do you really want to delete this blood sugar record ? ');
        if (result) {
            this.bloodSugarList.splice(index, 1);
        }
        return false;
    }
    //END BloodPressure
}


