import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute, ROUTER_DIRECTIVES } from "@angular/router";
import {Http, Response} from "@angular/http";

import { Account } from "../account/account.model";
import { Doctor } from "./doctor.model";
import { Patient } from "../patient/patient.model";

import { patientRepo } from "../utils/data/patients";
import { doctorRepo } from "../utils/data/doctors";

@Component({
    selector: 'doctor-connpatient',
    template: require("./connpatient.component.html"),
    styles: [
        require("../../sass/pages/connpatient.scss")
    ],
    directives: [ROUTER_DIRECTIVES]
})
export class ConnPatientComponent implements OnInit {
    private account: Account;
    private doctorID = '';
    private doctor: Doctor;

    private patients: Patient[] = [];

    private patientNameModel = '';

    constructor(private router: Router,
        private activatedRoute: ActivatedRoute) { }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));
        this.doctorID= this.activatedRoute.snapshot.params['acid'];

        this.patients = patientRepo.getAll();
    }

    searchConnectedPatient() {

        return false;
    }
}


