import {Component, OnInit} from "@angular/core";
import {Router, ROUTER_DIRECTIVES, ActivatedRoute} from "@angular/router";
import {NgForm} from "@angular/common";

import { Account} from "../account/account.model";
import { Prescription, Medicine, Attachment } from "./patient.model";

import { prescriptionRepo, medicines, images } from "../utils/data/prescriptions";

@Component({
   selector: 'patient-prescription-details',
   template: require('./patient.prescription.detail.component.html'),
   styles: [
      require('../../sass/pages/prescription.detail.scss')
   ], 
   directives: [ROUTER_DIRECTIVES],
   providers: []
})
export class PrescriptionDetailComponent implements OnInit{
    private account: Account;
    private patientID = '';
    private prescriptionID = '';

    private prescription: Prescription = new Prescription();
    private medicines: Medicine[] = [];
    private attachments: Attachment[] = [];
    
    constructor(private router: Router,
                private activatedRoute: ActivatedRoute) {}

    ngOnInit(){
        this.account = JSON.parse(window.localStorage.getItem('account'));
        this.patientID = this.activatedRoute.snapshot.params['acid'];
        this.prescriptionID = this.activatedRoute.snapshot.params['pid'];

        this.prescription = prescriptionRepo.getPrescriptionByID(this.prescriptionID);
        this.medicines = medicines;
        this.attachments = images;

    }

    toggleMedicineContainer() {
        let medicineContainer = document.getElementById('dp-meds-content');
        if (medicineContainer.style.display == 'none') {
            medicineContainer.style.display = 'block';
            let medFormI = document.getElementById('meds-header').firstElementChild;
            medFormI.className = 'fa fa-angle-up';
        } else {
            medicineContainer.style.display = 'none';
            let medFormI = document.getElementById('meds-header').firstElementChild;
            medFormI.className = 'fa fa-angle-down';
        }

        return false;
    }

    toggleAttachmentContainer() {
        let medicineContainer = document.getElementById('dp-imgatt-content');
        if (medicineContainer.style.display == 'block') {
            medicineContainer.style.display = 'none';
            let medFormI = document.getElementById('att-header').firstElementChild;
            medFormI.className = 'fa fa-angle-down';
        } else {
            medicineContainer.style.display = 'block';
            let medFormI = document.getElementById('att-header').firstElementChild;
            medFormI.className = 'fa fa-angle-up';
        }

        return false;
    }

    goToEditPrescription() {
        this.router.navigate([`/patients/${this.account.id}/medrecords/${this.prescription.medicalRecord.id}/prescriptions/${this.prescription.id}/edit`]);
        return false;
    }

    deletePrescription() {
        let answer = confirm('Do you really want to delete this prescription ?');
        if (answer) {
            prescriptionRepo.removePrescriptionByID(this.prescription.id);
            this.router.navigate([`/patients/${this.account.id}/prescriptions`]);
        }
        
        return false;
    }
}


