import { provideRouter, RouterConfig } from '@angular/router';

import { SearchDoctorsComponent } from "./doctors.component";

export const searchRoutes: RouterConfig = [
    {   
        path: 'profiles/search/doctors', 
        component: SearchDoctorsComponent,
    },
];



