import { Component, OnInit } from "@angular/core";
import { ROUTER_DIRECTIVES, Router } from '@angular/router';
import { Http, Response } from "@angular/http";
import { Schedule } from 'primeng/primeng';

import { AppointsPendingComponent } from "./patient.appoints.pending.component";
import { AppointsTotalComponent } from "./patient.appoints.activated.component";
import { AppointBrowserComponent } from "./patient.appoint.browser.component";

import { AppointmentOutput } from "./patient.model";
import { PatientAppointmentsServive } from "./patient.appoints.service";
import { Account } from "../account/account.model";

@Component({
    selector: 'patient-appoint',
    template: require("./patient.appoints.component.html"),
    styles: [
        require("../../sass/pages/appoint.scss"),
    ],

    providers: [PatientAppointmentsServive],
    directives: [ROUTER_DIRECTIVES],
    precompile: [AppointsPendingComponent, AppointsTotalComponent, AppointBrowserComponent]
})
export class AppointsComponent implements OnInit {
    private account: Account;
    private noPendingAppoints = 0;

    constructor(private appointService: PatientAppointmentsServive) {

    }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));

        this.noPendingAppoints = 149;

    }

    // START Add new appointment
    openAddNewAppointment() {
        let modal = document.getElementById('addnew-appoint');
        modal.style.display = "block";
        let btnAdd = document.getElementById('btn-add-appoint');
        btnAdd.style.opacity = '0.5';
        btnAdd.style.pointerEvents = 'none';
        return false;
    }

    closeAddNewAppointmentModel() {
        let modal = document.getElementById('addnew-appoint');
        modal.style.display = "none";
        let btnAdd = document.getElementById('btn-add-appoint');
        btnAdd.style.opacity = '1';
        btnAdd.style.pointerEvents = 'auto';
        return false;
    }

    addNewAppointment() {
        // Save data

        // Close add new appointment container
        let modal = document.getElementById('addnew-appoint');
        modal.style.display = "none";
        let btnAdd = document.getElementById('btn-add-appoint');
        btnAdd.style.opacity = '1';
        btnAdd.style.pointerEvents = 'auto';
        return false;
    }
    // END add new appointment

    diagnostic() {
        return JSON.stringify(null);
    }
}


