import { Account } from "../account/account.model";

export class Patient {
    id: string;
    firstName: string = '';
    lastName: string = '';
    email: string = '';
    password: string;
    dob: string;
    gender: number;
    phone: string;
    role: number;
    status: number;
    address: string;
    long: number;
    lat: number;
    photo: string = 'https://unsplash.it/g/200';
    created: string;
    lastModified: string;
    money: number;
    rank: number;
    noOfVoters: number;
}

export class AppointmentOutput {
    id: string;
    partner: Account = new Account();
    note: string;
    start: string;
    end: string;
    status: number;
    created: string;
    lastModified: string;
}

export class Allergy {
    id: string;
    name: string;
    cause: string;
    note: string;
    created: string;
    lastModified: string;
}

export class Addiction {
    id: string;
    cause: string;
    note: string;
    created: string;
    lastModified: string;
}

export class BloodSugar {
    id: string;
    value: number;
    note: string;
    time: string;
    created: string;
    lastModified: string;
}

export class BloodPressure {
    id: string;
    diastole: number;
    systole: number;
    note: string;
    time: string;
    created: string;
    lastModified: string;
}

export class HeartBeat {
    id: string;
    rate: number;
    note: string;
    time: string;
    created: string;
    lastModified: string;
}

export class Prescription {
    id: string;
    owner: Account;
    creator: Account;
    medicalRecord: MedicalRecord;
    from: string;
    to: string;
    note: string;
    created: string;
    lastModified: string;
}

export class Medicine {
    id: string;
    name: string;
    quantity: number;
    unit: string;
    note: string;
}

export class Comment {
    id: string;
    owner: Account;
    toPerson: Account;
    rate: number;
    content: string;
    created: string;
}

export class Attachment {
    file: string;
    description: string;

    constructor(file ?: string, desc?: string) {
        this.file = file || '';
        this.description = desc || '';
    }
}

export class MedicalDiary {
    id: string;
    owner: Account;
    content: string;
    date: string;
    created: string;
}

export class MedicalRecordCategory {
    id: string;
    name: string;
    description: string;

    constructor(id?: string, name?: string, desc?: string) {
        this.id = id || '';
        this.name = name || '';
        this.description = desc || '';
    }
}
export class MedicalRecord {
    id: string;
    owner: Account;
    category: MedicalRecordCategory;
    date: string;
    info: string;
    created: string;
    lastModified: string;
}

export class MedicalImage {
    id: string;
    medicalRecord: MedicalRecord;
    owner: Account;
    file: string;
    created: string;
}

export class ExperimentNote {
    id: string;
    medicalRecord: MedicalRecord;
    title: string;
    content: any;
    created: string;
    lastModified: string;
}


