import {Prescription, Medicine, Attachment} from "../../patient/patient.model";
import { ISO8601DateTimeFormat, ISO8601DateFormat } from "../constants";
import { doctorRepo } from "./doctors";
import { patientRepo} from "./patients";
import {medicalRecordRepo} from "./medrecords";

class PrescriptionRepo {
    private prescriptionArr: Prescription[] = [];

    constructor() {
        let pre = new Prescription();
        pre.id = '1231e324rc';
        pre.creator = doctorRepo.getAll()[2];
        pre.owner = patientRepo.getAll()[0];
        pre.medicalRecord = medicalRecordRepo.getAll()[0];
        pre.from = moment().subtract(4, 'days').format(ISO8601DateFormat);
        pre.to = moment().format(ISO8601DateFormat);
        pre.note = ' Dolor repudiandae at mel, timeam mentitum partiendo ea vim. Usu cu case verterem evertitur, id prima hendrerit incorrupte mel. Has ut soluta petentium referrentur, mutat ipsum vocent te pri, mea probo definiebas scripserit et';
        pre.created = moment().format(ISO8601DateTimeFormat);
        pre.lastModified = moment().format(ISO8601DateTimeFormat);

        let pre2 = new Prescription();
        pre2.id = '1231e324rf';
        pre2.creator = patientRepo.getAll()[0];
        pre2.owner = patientRepo.getAll()[0];
        pre2.medicalRecord = medicalRecordRepo.getAll()[0];
        pre2.from = moment().subtract(4, 'days').format(ISO8601DateFormat);
        pre2.to = moment().format(ISO8601DateFormat);
        pre2.note = ' Dolor repudiandae at mel, timeam mentitum partiendo ea vim. Usu cu case verterem evertitur, id prima hendrerit incorrupte mel. Has ut soluta petentium referrentur, mutat ipsum vocent te pri, mea probo definiebas scripserit et';
        pre2.created = moment().format(ISO8601DateTimeFormat);
        pre2.lastModified = moment().format(ISO8601DateTimeFormat);

        this.prescriptionArr.push(pre);
        this.prescriptionArr.push(pre2);
    }

    getAll() {
        return this.prescriptionArr;
    }

    getPrescriptionByID(id: string) {
        for (let item of this.prescriptionArr) {
            if (item.id == id) 
                return item;
        }
        return null;
    }

    removePrescriptionByID(id: string) {
        let temp: Prescription[] = [];
        for (let item of this.prescriptionArr) {
            if (item.id != id) 
                temp.push(item);
        }
        this.prescriptionArr = temp;
        return temp;
    }
}

export let prescriptionRepo = new PrescriptionRepo();

