import { bootstrap } from '@angular/platform-browser-dynamic';
import { enableProdMode, Injectable, provide } from '@angular/core';
import { BaseResponseOptions, RequestOptions, HTTP_PROVIDERS } from "@angular/http";
import { AppComponent } from './app/app.component';
import { APP_ROUTER_PROVIDERS } from './app/app.routes';

if (process.env.ENV === 'production') {
  enableProdMode();
}

@Injectable()
export class ExRequestOptions extends BaseResponseOptions{

  constructor() {
    super()
    this.headers.append('X-CSRFToken', this.getCookie('csrftoken'));
  }

  getCookie(name: string) {
    let value = "; " + document.cookie;
    let parts = value.split("; " + name + "=");
    if (parts.length == 2) 
      return parts.pop().split(";").shift();
  }
}


bootstrap(AppComponent, [
  APP_ROUTER_PROVIDERS,
  HTTP_PROVIDERS,
  provide(RequestOptions, {useClass: ExRequestOptions})
]).catch(err => console.error(err));



