import { AppointmentOutput } from "../../patient/patient.model";
import * as moment from 'moment';

export class AppointOutRepo {
    private appoints: AppointmentOutput[] = [];

    constructor() {
        let appoint: AppointmentOutput = new AppointmentOutput();
        appoint.id = '123et123';
        appoint.partner.firstName = 'Trọng';
        appoint.partner.lastName = 'Bùi';
        appoint.start = '12-07-2016 10:30:00';
        appoint.end = '12-07-2016 12:30:00';
        appoint.note = "Mỗi lần Tiến quân ca vang lên trong những thời khắc thiêng liêng, tinh thần yêu nước lại dâng trào. Tôi được nghe những người lính kể rằng giai điệu bài Tiến quân ca đã tiếp thêm cho họ sức mạnh trước mỗi trận đánh khốc liệt. Với tôi, nhạc và lời ca khúc đã thân thuộc từ thủa nhỏ, qua bao thay đổi của thời cuộc, nay tuổi đã cao nhưng tôi vẫn không khỏi xúc động mỗi khi cùng mọi người hát vang Quốc ca trong các sự kiện lớn.";
        appoint.created = moment().format('DD-MM-YYYY HH:mm:ss');
        appoint.lastModified = moment().format('DD-MM-YYYY HH:mm:ss');;
        appoint.status = 0;

        let appoint2: AppointmentOutput = new AppointmentOutput();
        appoint2.id = '131rwer6';
        appoint2.partner.firstName = 'Cobie';
        appoint2.partner.lastName = 'Smulders';
        appoint2.start = '12-07-2016 10:30:00';
        appoint2.end = '12-07-2016 10:30:00';
        appoint2.note = "Lorem ipsum dolor sit amet, per te diam apeirian. Quem ludus integre in mel, tation contentiones est et. Pri omnes saperet docendi id, te pro elit corpora deleniti. Et qui labores definiebas, mazim deserunt voluptaria mei et";
        appoint2.created = moment().format('DD-MM-YYYY HH:mm:ss');;
        appoint2.lastModified = moment().format('DD-MM-YYYY HH:mm:ss');;
        appoint2.status = 2;

        let appoint3: AppointmentOutput = new AppointmentOutput();
        appoint3.id = '131rwer7';
        appoint3.partner.firstName = 'Sergey';
        appoint3.partner.lastName = 'Brin';
        appoint3.start = '12-07-2017 10:30:00';
        appoint3.end = '12-07-2017 10:30:00';
        appoint3.note = "Lorem ipsum dolor sit amet, per te diam apeirian. Quem ludus integre in mel, tation contentiones est et. Pri omnes saperet docendi id, te pro elit corpora deleniti. Et qui labores definiebas, mazim deserunt voluptaria mei et";
        appoint3.created = moment().format('DD-MM-YYYY HH:mm:ss');;
        appoint3.lastModified = moment().format('DD-MM-YYYY HH:mm:ss');;
        appoint3.status = 3;

        this.appoints.push(appoint);
        this.appoints.push(appoint2);
        this.appoints.push(appoint3);
    }

    get(id: string) {
        for (let appoint of this.appoints) {
            if (appoint.id == id) {
                return appoint;
            }
        }
    }

    getAllAppoints() {
        return this.appoints;
    }

    add(appoint: AppointmentOutput) {
        this.appoints.push(appoint);
    }
    
}


export let appointRepo = new AppointOutRepo();


