import { Component} from "@angular/core";
import { ROUTER_DIRECTIVES, Router } from '@angular/router';
import { Http, Response} from "@angular/http";

import { AppointsComponent } from "./patient.appoints.component";
import { ProfileComponent } from "./profile.component";
import { AppointDetailsComponent } from "./patient.appoint.details.component";
import { PrescriptionComponent } from "./patient.prescription.component";
import { AddPrescriptionComponent } from "./patient.prescription.add.component";
import { EditPrescriptionComponent } from "./patient.prescription.edit.component";
import { PrescriptionDetailComponent } from "./patient.prescription.detail.component";
import { StatsBPPatientComponent } from "./patient.stats.bp.component";
import { StatsBSPatientComponent } from "./patient.stats.bs.component";
import { StatsHBPatientComponent } from "./patient.stats.hb.component";
import { MedRecordPatientComponent } from "./patient.medrecord.component";
import { MedRecordDetailsPatientComponent } from "./patient.medrecord.details.component";
import { MedIndexPatientComponent } from "./patient.medindex.component";
import { MedDiaryPatientComponent } from "./patient.meddiary.component";
import { ExpNoteDetailsComponent } from "./patient.expnote.details.component";
import { ExpNoteAddComponent } from "./patient.expnote.add.component";

import { Account } from '../account/account.model';

@Component({
    selector: 'patient',
    template: require("./patient.component.html"),
    styles: [
        require("../../sass/pages/patient.scss")
    ],
    directives: [ROUTER_DIRECTIVES],
    precompile: [AppointsComponent,
        ProfileComponent,
        AppointDetailsComponent,
        PrescriptionComponent,
        StatsBPPatientComponent,
        StatsBSPatientComponent,
        StatsHBPatientComponent,
        MedRecordPatientComponent,
        MedIndexPatientComponent,
        MedDiaryPatientComponent,
        MedRecordDetailsPatientComponent,
        ExpNoteAddComponent,
        ExpNoteDetailsComponent,
        ]
})
export class PatientComponent {
    private account: Account;

    constructor(private router: Router) { }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));

    }
}


