export class Doctor {
    id: string;
    firstName: string = '';
    lastName: string = '';
    email: string = '';
    password: string;
    dob: string;
    gender: number;
    phone: string;
    role: number;
    status: number;
    address: string;
    long: number;
    lat: number;
    photo: string = 'https://unsplash.it/g/200';
    created: string;
    lastModified: string;
    money: number;
    rank: number;
    speciality: string;
    noOfVoters: number;
}



