import {Component, OnInit} from "@angular/core";
import {Router, ActivatedRoute, ROUTER_DIRECTIVES} from "@angular/router";
import {NgForm} from "@angular/common";

import { Account} from "../account/account.model";
import { Prescription } from "./patient.model";

import { prescriptionRepo } from "../utils/data/prescriptions";

@Component({
   selector: 'patient-prescriptions',
   template: require('./patient.prescription.component.html'),
   styles: [
      require('../../sass/pages/prescription.scss')
   ], 
   directives: [ROUTER_DIRECTIVES]
})

export class PrescriptionComponent implements OnInit{
    private account: Account;
    private patientID = '';

    private cprescriptions: Prescription[] = [];
    private hprescriptions: Prescription[] = [];
    
    constructor(private router: Router,
                private activatedRoute: ActivatedRoute) {}

    ngOnInit(){
        this.account = JSON.parse(window.localStorage.getItem('account'));
        this.patientID = this.activatedRoute.snapshot.params['acid'];
        
        this.hprescriptions = prescriptionRepo.getAll();
        this.cprescriptions = prescriptionRepo.getAll();
    }

    goToAddNewPrescription() {
        this.router.navigate([`/patients/${this.account.id}/prescriptions/add`]);
    }

}


