import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute, ROUTER_DIRECTIVES } from "@angular/router";
import {Http, Response} from "@angular/http";

import { Account } from "../account/account.model";
import { Patient, MedicalRecord, MedicalRecordCategory } from "./patient.model";
import { Doctor } from "../doctor/doctor.model";

import { patientRepo } from "../utils/data/patients";
import { medicalRecordRepo, medrecordCategories } from "../utils/data/medrecords";

@Component({
    selector: 'patient-stats',
    template: require("./patient.medrecord.component.html"),
    styles: [
        require("../../sass/pages/medrecord.scss")
    ],
    directives: [ROUTER_DIRECTIVES]
})
export class MedRecordPatientComponent implements OnInit {
    private account: Account;
    private patientID = '';
    private patient: Patient;

    private medicalRecords: MedicalRecord[] = [];
    private medicalRecordModel: MedicalRecord = new MedicalRecord();
    private medCategories: MedicalRecordCategory[] = [];

    constructor(private router: Router,
        private activatedRoute: ActivatedRoute) { }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));
        this.patientID = this.activatedRoute.snapshot.params['acid'];
        this.patient = patientRepo.getPatientByID(this.patientID);
        
        this.medicalRecords = medicalRecordRepo.getAll();
        this.medCategories = medrecordCategories;
    }

    openAddNewMedicalRecord() {
        this.medicalRecordModel = new MedicalRecord();

        let recordForm = document.getElementById('medrecord-add-form');
        recordForm.style.display = "block";
        let btnOpenAdd = document.getElementById('btn-add-medrecord');
        btnOpenAdd.style.opacity = '0.6';
        btnOpenAdd.style.pointerEvents = 'none';
        return false;
    }

    saveNewMedicalRecord() {


        let recordForm = document.getElementById('medrecord-add-form');
        recordForm.style.display = "none";
        let btnOpenAdd = document.getElementById('btn-add-medrecord');
        btnOpenAdd.style.opacity = '1';
        btnOpenAdd.style.pointerEvents = 'auto';
        return false;
    }

    closeAddNewMedicalRecord() {
        let recordForm = document.getElementById('medrecord-add-form');
        recordForm.style.display = "none";
        let btnOpenAdd = document.getElementById('btn-add-medrecord');
        btnOpenAdd.style.opacity = '1';
        btnOpenAdd.style.pointerEvents = 'auto';

        return false;
    }

    goToMedicalRecordDetails(item: MedicalRecord) {
        this.router.navigate([`/patients/${this.account.id}/medrecords/`, item.id]);
    }
}


