export let medrecordCategories: MedicalRecordCategory[] = [cate, cate2, cate3, cate4];


class MedicalRecordRepo {
    private medRecords: MedicalRecord[] = [];

    constructor() {
        let pre = new MedicalRecord();
        pre.id = '1231e324rc';
        pre.owner = doctorRepo.getAll()[0];
        pre.info = 'Dolor repudiandae at mel, timeam mentitum partiendo ea vim. Usu cu case verterem evertitur, id prima hendrerit incorrupte mel. Has ut soluta petentium referrentur, mutat ipsum vocent te pri, mea probo definiebas scripserit et';
        pre.date = moment().format(ISO8601DateTimeFormat);
        pre.category = medrecordCategories[0];
        pre.created = moment().format(ISO8601DateTimeFormat);
        pre.lastModified = moment().format(ISO8601DateTimeFormat);

        let pre2 = new MedicalRecord();
        pre2.id = '1231e324rf';
        pre2.owner = doctorRepo.getAll()[0];
        pre2.info = 'Dolor repudiandae at mel, timeam mentitum partiendo ea vim. Usu cu case verterem evertitur, id prima hendrerit incorrupte mel. Has ut soluta petentium referrentur, mutat ipsum vocent te pri, mea probo definiebas scripserit et';
        pre2.date = moment().format(ISO8601DateTimeFormat);
        pre2.category = medrecordCategories[0];
        pre2.created = moment().format(ISO8601DateTimeFormat);
        pre2.lastModified = moment().format(ISO8601DateTimeFormat);
        
        let pre3 = new MedicalRecord();
        pre3.id = '1231e324rv';
        pre3.owner = doctorRepo.getAll()[0];
        pre3.info = 'Dolor repudiandae at mel, timeam mentitum partiendo ea vim. Usu cu case verterem evertitur, id prima hendrerit incorrupte mel. Has ut soluta petentium referrentur, mutat ipsum vocent te pri, mea probo definiebas scripserit et';
        pre3.date = moment().format(ISO8601DateTimeFormat);
        pre3.category = medrecordCategories[1];
        pre3.created = moment().format(ISO8601DateTimeFormat);
        pre3.lastModified = moment().format(ISO8601DateTimeFormat);
        
        let pre4 = new MedicalRecord();
        pre4.id = '1231e324rz';
        pre4.owner = doctorRepo.getAll()[0];
        pre4.info = 'Dolor repudiandae at mel, timeam mentitum partiendo ea vim. Usu cu case verterem evertitur, id prima hendrerit incorrupte mel. Has ut soluta petentium referrentur, mutat ipsum vocent te pri, mea probo definiebas scripserit et';
        pre4.date = moment().format(ISO8601DateTimeFormat);
        pre4.category = medrecordCategories[3];
        pre4.created = moment().format(ISO8601DateTimeFormat);
        pre4.lastModified = moment().format(ISO8601DateTimeFormat);

        this.medRecords.push(pre);
        this.medRecords.push(pre2);
        this.medRecords.push(pre3);
        this.medRecords.push(pre4);
    }

    getAll() {
        return this.medRecords;
    }

    getMedicalRecordByID(id: string) {
        for (let item of this.medRecords) {
            if (item.id == id) 
                return item;
        }
        return null;
    }

    removeMedicalRecordByID(id: string) {
        let temp: MedicalRecord[] = [];
        for (let item of this.medRecords) {
            if (item.id != id) 
                temp.push(item);
        }
        this.medRecords = temp;
        return temp;
    }
}

export let medicalRecordRepo = new MedicalRecordRepo();


