import {Component, OnInit} from "@angular/core";
import {NgForm} from "@angular/common";
import {Router, ROUTER_DIRECTIVES} from "@angular/router";
import Logger from "../utils/logger.service";
import Country from "../utils/country";
import City from "../utils/city";
import {AccountService} from "./account.service";
import {SignupDoctorAccount, Account} from "./account.model";

@Component({
   selector: 'signup-doctor',
   template: require('./signup-doctor.component.html'),
   styles: [
      require('../../sass/pages/signup.doctor.scss')
   ],  
   directives: [ROUTER_DIRECTIVES],
   providers: [AccountService]
})

export class SignupDoctorComponent implements OnInit{
    private account: Account;
    private model: SignupDoctorAccount;
    private logger: Logger;
    private countries: Array<Country>;
    private cities: Array<City>;
    private countrySelectionDisabled = false;
    
    constructor(private router: Router,
                private accountService: AccountService) {
        this.logger = new Logger("SignupComponent");      
        this.model = new SignupDoctorAccount();
    }
    
    ngOnInit() {
        this.countries = [new Country("vie", "Viet Nam"), new Country("US", "United States")];
        this.cities = [new City("12312w3", "vie", "Hanoi"), new City("312e32", "vie", "Ho Chi Minh")];

        // Check account already signed in
        this.account = JSON.parse(window.localStorage.getItem('account'));
        if (this.account && this.account.role == 0) {
            this.router.navigate([`/patients/${this.account.id}/appointments`]);
        } else if (this.account && this.account.role == 1) {
            this.router.navigate([`/patients/${this.account.id}/appointments`]);
        } 
    }
    
    signUp() {
        this.logger.log("On submitting...");

        // Request signup service
        
    }

    onChangeCitySelection() {
        this.countrySelectionDisabled = false;
    }
    
    get diagnostic() {
        return JSON.stringify(this.model);
    }
} 



