import { Addiction } from "../../patient/patient.model";
import { ISO8601DateTimeFormat } from "../constants";

class AddictionRepo {
    private addictions: Addiction[] = [];

    constructor() {
        let addic = new Addiction();
        addic.id = '1231e324rt';
        addic.cause = 'Pro ei tation iisque';
        addic.note = 'Eam amet accusata cu. Et quo modus referrentur, iriure atomorum ullamcorper cu nec';
        addic.created = moment().format(ISO8601DateTimeFormat);
        addic.lastModified = moment().format(ISO8601DateTimeFormat);

        let addic2 = new Addiction();
        addic2.id = '1231e324rt';
        addic2.cause = 'Elit elitr rationibus vix ea. Et mei eius natum assum';
        addic2.note = ' Dolor repudiandae at mel, timeam mentitum partiendo ea vim. Usu cu case verterem evertitur, id prima hendrerit incorrupte mel. Has ut soluta petentium referrentur, mutat ipsum vocent te pri, mea probo definiebas scripserit et';
        addic2.created = moment().format(ISO8601DateTimeFormat);
        addic2.lastModified = moment().format(ISO8601DateTimeFormat);

        this.addictions.push(addic);
        this.addictions.push(addic2);
    }

    getAll() {
        return this.addictions;
    }

    getAddictionByID(id: string) {
        for (let item of this.addictions) {
            if (item.id == id) 
                return item;
        }

        return null;
    }
}

export let addictionRepo = new AddictionRepo();


