import { Comment } from "../../patient/patient.model";
import { doctorRepo } from "./doctors";
import { patientRepo } from "./patients";
import * as moment from 'moment';
import { ISO8601DateTimeFormat, ISO8601DateFormat } from "../constants";


export class CommentRepo {
    private comments: Comment[] = [];

    constructor() {
        let comment: Comment = new Comment();
        comment.id = '123et123';
        comment.owner = doctorRepo.getAll()[0];
        comment.toPerson = patientRepo.getAll()[0];
        comment.rate = 5;
        comment.content = "Lorem ipsum dolor sit amet, quot commodo molestie id vis, no vim wisi veniam patrioque. Choro tollit democritum no mei, maiorum habemus et est. Vim id congue invenire percipitur. Ad saperet indoctum mnesarchum vis, est ei nostro iriure corpora";
        comment.created = moment().format(ISO8601DateFormat);
        
        let comment2: Comment = new Comment();
        comment2.id = '123et124';
        comment2.owner = doctorRepo.getAll()[0];
        comment2.toPerson = patientRepo.getAll()[0];
        comment2.rate = 4;
        comment2.content = "Lorem ipsum dolor sit amet, quot commodo molestie id vis, no vim wisi veniam patrioque. Choro tollit democritum no mei, maiorum habemus et est. Vim id congue invenire percipitur. Ad saperet indoctum mnesarchum vis, est ei nostro iriure corpora";
        comment2.created = moment().format(ISO8601DateFormat);

        let comment3: Comment = new Comment();
        comment3.id = '123et126';
        comment3.owner = doctorRepo.getAll()[0];
        comment3.toPerson = patientRepo.getAll()[0];
        comment3.rate = 1;
        comment3.content = "Lorem ipsum dolor sit amet, quot commodo molestie id vis, no vim wisi veniam patrioque. Choro tollit democritum no mei, maiorum habemus et est. Vim id congue invenire percipitur. Ad saperet indoctum mnesarchum vis, est ei nostro iriure corpora";
        comment3.created = moment().format(ISO8601DateFormat);


        this.comments.push(comment);
        this.comments.push(comment2);
        this.comments.push(comment3);
        this.comments.push(comment2);
    }

    get(id: string) {
        for (let appoint of this.comments) {
            if (appoint.id == id) {
                return appoint;
            }
        }
    }

    getAllComments() {
        return this.comments;
    }

    add(comment: Comment) {
        this.comments.push(comment);
    }
    
}


export let commentRepo = new CommentRepo();


