import {Component, OnInit} from "@angular/core";
import { Router, ROUTER_DIRECTIVES } from "@angular/router";
import {Http, Response} from "@angular/http";
import { Account } from "../account/account.model";
import { Doctor } from "../doctor/doctor.model";
import { SearchService } from "./search.service";

import { doctorRepo } from "../utils/data/doctors";

@Component({
    selector: 'profile-searchdoctors',
    template: require("./doctors.component.html"),
    styles: [
        require("../../sass/pages/search.doctor.scss"),
    ],
    directives: [ROUTER_DIRECTIVES],
    providers: [SearchService]
})
export class SearchDoctorsComponent implements OnInit {
    private account: Account;
    private matchingDoctors: Doctor[] = [];

    private fullNameInput: string;
    private specialtyIDInput: string;
    private countryIDInput: string;
    private cityIDInput: string;
    private citySelectDisabled = true;

    constructor(private router: Router, private searchService: SearchService) {
        
    }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));
    }

    searchDoctors() {

        // this.searchService.searchDoctor(this.account, null, null, null, null)
        //             .subscribe(
        //                 success => {
        //                     if (success.status == 1) {
        //                         this.doctors = success.data;
        //                     }
        //                 },
        //                 error => {

        //                 }
        //             );
        this.matchingDoctors = doctorRepo.getAll();

        let searchResultElem = document.getElementById('search-result-container');
        searchResultElem.style.display = "block";
    }

    clearSearchInput() {

    }

    goToDoctorProfile(id: string) {
        this.router.navigate([`/doctors/${id}`]);

        return false;
    }

    onChangeCountrySelection() {
        this.citySelectDisabled = false;
    }

    diagnostic() {
        return JSON.stringify(this.matchingDoctors);
    }
}


