import { Component, OnInit } from "@angular/core";
import { ROUTER_DIRECTIVES, Router, ActivatedRoute} from '@angular/router';
import { Http, Response } from "@angular/http";
import { Schedule } from 'primeng/primeng';

import { AppointsPendingComponent } from "./doctor.appoints.pending.component";
import { AppointsTotalComponent } from "./doctor.appoints.activated.component";

import { AppointmentOutput } from "../patient/patient.model";
import { DoctorAppointmentsServive } from "./doctor.appoints.service";
import { Account } from "../account/account.model";

import { AppointOutRepo } from "../utils/data/appoints";

@Component({
    selector: 'doctor-appoint-details',
    template: require("./doctor.appoint.details.component.html"),
    styles: [
        require("../../sass/pages/appoint.details.scss"),
    ],

    providers: [DoctorAppointmentsServive],
    directives: [],
})
export class AppointDetailsComponent implements OnInit {
    private account: Account;
    private appoint: AppointmentOutput = new AppointmentOutput();
    private editingAppoint: EditAppoint;  

    constructor(private appointService: DoctorAppointmentsServive,
        private router: Router,
        private activatedRoute: ActivatedRoute) {

    }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));

        // get appointment's id
        let appointID = this.activatedRoute.snapshot.params['apid'];

        // Get all appointments
        // this.appointService.getAllApoints(this.account)
        //     .subscribe(
        //     success => {
        //         if (success.status == 1) {
        //             this.appoint = success.data[0];
        //         } else {

        //         }
        //     },
        //     error => {

        //     }
        //     );

        //fake data
        let aRepo: AppointOutRepo = new AppointOutRepo();
        this.appoint = aRepo.get(appointID);
        this.editingAppoint = new EditAppoint();

        if (this.appoint.status == 0 || this.appoint.status == 1 ) {
            let btnCancelAppoint = document.getElementById('btn-appoint-cancel');
            btnCancelAppoint.style.opacity = '0.6';
            btnCancelAppoint.style.pointerEvents = 'none';
        } else if (this.appoint.status == 2) {
            let btnAcceptAppoint = document.getElementById('btn-appoint-accept');
            btnAcceptAppoint.style.opacity = '0.6';
            btnAcceptAppoint.style.pointerEvents = 'none';
        } else {
            let btnCancelAppoint = document.getElementById('btn-appoint-cancel');
            btnCancelAppoint.style.opacity = '0.6';
            btnCancelAppoint.style.pointerEvents = 'none';
            let btnAcceptAppoint = document.getElementById('btn-appoint-accept');
            btnAcceptAppoint.style.opacity = '0.6';
            btnAcceptAppoint.style.pointerEvents = 'none';
        }

    }

    acceptAppointment() {

        return false;
    }

    cancelAppointment() {

        return false;
    }

    // START Edit appointment
    saveEditAppointment() {

        // Close edit appointment container
        let editArticle = document.getElementById('edit-appoint-container');
        let detailArticle = document.getElementById('details-container');
        editArticle.style.display = "none";
        detailArticle.style.display = "block";
        let btnEditAppoint = document.getElementById('btn-edit-appoint-details');
        btnEditAppoint.style.opacity = '1';
        btnEditAppoint.style.pointerEvents = 'auto';
        return false;
    }

    openEditAppointment() {
        this.editingAppoint.id = this.appoint.partner.id;
        this.editingAppoint.doctorName = `${this.appoint.partner.firstName} ${this.appoint.partner.lastName}`;
        this.editingAppoint.start = this.appoint.start;
        this.editingAppoint.end = this.appoint.end;
        this.editingAppoint.note = this.appoint.note;

        let editArticle = document.getElementById('edit-appoint-container');
        let detailArticle = document.getElementById('details-container');
        editArticle.style.display = "block";
        detailArticle.style.display = "none";
        let btnEditAppoint = document.getElementById('btn-edit-appoint-details');
        btnEditAppoint.style.opacity = '0.6';
        btnEditAppoint.style.pointerEvents = 'none';
        
        return false;
    }

    cancelEditingAppointmentModel() {
        let editArticle = document.getElementById('edit-appoint-container');
        let detailArticle = document.getElementById('details-container');
        editArticle.style.display = "none";
        detailArticle.style.display = "block";
        let btnEditAppoint = document.getElementById('btn-edit-appoint-details');
        btnEditAppoint.style.opacity = '1';
        btnEditAppoint.style.pointerEvents = 'auto';
        return false;
    }
    // END edit appointment

    diagnostic() {
        return JSON.stringify(this.appoint);
    }
}

class EditAppoint {
    id: string;
    doctorName: string;
    start: string;
    end: string;
    note: string;
}


