import {Component, OnInit} from "@angular/core";
import {Router, ActivatedRoute, ROUTER_DIRECTIVES} from "@angular/router";
import {NgForm} from "@angular/common";

import { Account} from "../account/account.model";
import { ExperimentNote } from "./patient.model";

import { prescriptionRepo, medicines, images } from "../utils/data/prescriptions";

@Component({
   selector: 'patient-prescription-details',
   template: require('./patient.expnote.add.component.html'),
   styles: [
      require('../../sass/pages/expnote.add.scss')
   ], 
   directives: [ROUTER_DIRECTIVES],
   providers: []
})
export class ExpNoteAddComponent implements OnInit{
    private account: Account;

    private expNote: ExperimentNote = new ExperimentNote();
    private medicalRecordID = '';

    constructor(private router: Router,
                private activatedRoute: ActivatedRoute) {}

    ngOnInit(){
        this.account = JSON.parse(window.localStorage.getItem('account'));
        this.medicalRecordID = this.activatedRoute.snapshot.params['mrid'];    
    }

    saveNewExpNote() {

        this.expNote.id = '1231e324rf';
        this.router.navigate([`/patients/${this.account.id}/medrecords/${this.medicalRecordID}/expnotes/`, this.expNote.id]);
        return false;
    }
}


