import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute, ROUTER_DIRECTIVES } from "@angular/router";
import {Http, Response} from "@angular/http";

import { Account } from "../account/account.model";
import { MedicalDiary} from "./patient.model";
import { Doctor } from "../doctor/doctor.model";

import { patientRepo } from "../utils/data/patients";
import { diaryRepo } from "../utils/data/diaries";

@Component({
    selector: 'patient-stats',
    template: require("./patient.meddiary.component.html"),
    styles: [
        require("../../sass/pages/meddiary.scss")
    ],
    directives: [ROUTER_DIRECTIVES]
})
export class MedDiaryPatientComponent implements OnInit {
    private account: Account;

    private diaries: MedicalDiary[] = [];
    private diaryModel: MedicalDiary = new MedicalDiary();
    private eddiaryModel: MedicalDiary = new MedicalDiary();

    constructor(private router: Router,
        private activatedRoute: ActivatedRoute) { }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));

        this.diaries = diaryRepo.getAll();
    }

    saveNewDiary() {

        
        let diaryForm = document.getElementById('diary-form');
        diaryForm.style.display = "none";
        let btnOpenAdd = document.getElementById('btn-add-diary');
        btnOpenAdd.style.opacity = '1';
        btnOpenAdd.style.pointerEvents = 'auto';

        return false;
    }

    updateDiary(index: number) {

        let formID = `ediary-form-${index}`;
        let editForm = document.getElementById(formID);
        editForm.style.display = 'none';
        let contentID = `ediary-content-${index}`;
        let contentElem = document.getElementById(contentID)
        contentElem.style.display = 'block';
        return false;
    }

    deleteDiary(index: number) {
        this.diaries.splice(index, 1);

        let dropdownSubmenus = document.getElementsByClassName('diary-item-dropdown');
        for (let i = 0; i < dropdownSubmenus.length; i++) {
            let htmlElem = <HTMLElement>dropdownSubmenus[i];
            htmlElem.style.display = "none";
            htmlElem.setAttribute('data-isopened', '0');
        }
        return false;
    }

    openAddNewDiary() {
        this.diaryModel = new MedicalDiary();

        let diaryForm = document.getElementById('diary-form');
        diaryForm.style.display = "block";
        let btnOpenAdd = document.getElementById('btn-add-diary');
        btnOpenAdd.style.opacity = '0.6';
        btnOpenAdd.style.pointerEvents = 'none';
        return false;
    }

    closeAddNewDiary() {
        let diaryForm = document.getElementById('diary-form');
        diaryForm.style.display = "none";
        let btnOpenAdd = document.getElementById('btn-add-diary');
        btnOpenAdd.style.opacity = '1';
        btnOpenAdd.style.pointerEvents = 'auto';
        return false;
    }

    openEditDiaryForm(index: number, item: MedicalDiary) {
        this.eddiaryModel.id = item.id;
        this.eddiaryModel.date = item.date;
        this.eddiaryModel.content = item.content;

        // Change style
        for (let i=0; i < this.diaries.length; i++) {
            let editForm = document.getElementById(`ediary-form-${i}`);
            if (editForm) {
                editForm.style.display = 'none';
            }
        }
        for (let i=0; i < this.diaries.length; i++) {
            let contentElem = document.getElementById(`ediary-content-${i}`);
            if (contentElem) {
                contentElem.style.display = 'block';
            }
        }
        let formID = `ediary-form-${index}`;
        let editForm = document.getElementById(formID);
        editForm.style.display = 'block';
        let contentID = `ediary-content-${index}`;
        let contentElem = document.getElementById(contentID)
        contentElem.style.display = 'none';
        let dropdownSubmenus = document.getElementsByClassName('diary-item-dropdown');
        for (let i = 0; i < dropdownSubmenus.length; i++) {
            let htmlElem = <HTMLElement>dropdownSubmenus[i];
            htmlElem.style.display = "none";
            htmlElem.setAttribute('data-isopened', '0');
        }
        return false;
    }

    closeEditDiary(index: number) {
        let formID = `ediary-form-${index}`;
        let editForm = document.getElementById(formID);
        editForm.style.display = 'none';
        let contentID = `ediary-content-${index}`;
        let contentElem = document.getElementById(contentID)
        contentElem.style.display = 'block';
        return false;
    }

    windowClickDropdownHandler(event: any) {
        let targetElem = <HTMLElement>event.target;
        if (!targetElem.classList.contains('dropdown-item')
            && !targetElem.classList.contains('btn-submenu-i')) {
            let dropdownSubmenus = document.getElementsByClassName('diary-item-dropdown');
            for (let i = 0; i < dropdownSubmenus.length; i++) {
                let htmlElem = <HTMLElement>dropdownSubmenus[i];
                htmlElem.style.display = "none";
                htmlElem.setAttribute('data-isopened', '0');
            }
        }
    }


    openSubMenuDiaryItem(index: number) {
        let menuID = `diary-item-${index}`;
        let diarySubmenu = document.getElementById(menuID);
        if (diarySubmenu) {
            let accIsOpened = diarySubmenu.getAttribute('data-isopened');
            if (accIsOpened && accIsOpened == '0') {
                let dropdownSubmenus = document.getElementsByClassName('diary-item-dropdown');
                for (let i = 0; i < dropdownSubmenus.length; i++) {
                    let htmlElem = <HTMLElement>dropdownSubmenus[i];
                    htmlElem.style.display = "none";
                    htmlElem.setAttribute('data-isopened', '0');
                }
                diarySubmenu.style.display = "block";
                diarySubmenu.setAttribute('data-isopened', '1');
                window.addEventListener('click', this.windowClickDropdownHandler);
            } else {
                diarySubmenu.style.display = "none";
                diarySubmenu.setAttribute('data-isopened', '0');
                window.removeEventListener('click', this.windowClickDropdownHandler);
            }
        }
    }
}


