import {Component, OnInit} from "@angular/core";
import {Router, ROUTER_DIRECTIVES, ActivatedRoute} from "@angular/router";
import {NgForm} from "@angular/common";

import { Account} from "../account/account.model";
import { ExperimentNote } from "./patient.model";

import { expNoteRepo } from "../utils/data/medrecords";

@Component({
    selector: 'patient-expnote-details',
    template: require('./patient.expnote.details.component.html'),
    styles: [
        require('../../sass/pages/expnote.details.scss')
    ],
    directives: [ROUTER_DIRECTIVES],
    providers: []
})
export class ExpNoteDetailsComponent implements OnInit {
    private account: Account;

    private expNote: ExperimentNote = new ExperimentNote();
    private edexpNote: ExperimentNote = new ExperimentNote();

    constructor(private router: Router,
        private activatedRoute: ActivatedRoute) { }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));
        let expNoteID = this.activatedRoute.snapshot.params['enid'];
        this.expNote = expNoteRepo.getExperimentNoteByID(expNoteID);
    }

    openEditExpNote() {
        this.edexpNote.title = this.expNote.title;
        
        let editForm = document.getElementById('expnote-edit-content');
        let contentDiv = document.getElementById('expnote-content');
        editForm.style.display = 'block';
        contentDiv.style.display = 'none';
        let btnOpenEdit = document.getElementById('btn-edit-expnote');
        btnOpenEdit.style.opacity = '0.5';
        btnOpenEdit.style.pointerEvents = 'none';
        return false;
    }
    closeEditExpNote() {
        let editForm = document.getElementById('expnote-edit-content');
        let contentDiv = document.getElementById('expnote-content');
        editForm.style.display = 'none';
        contentDiv.style.display = 'block';
        let btnOpenEdit = document.getElementById('btn-edit-expnote');
        btnOpenEdit.style.opacity = '1';
        btnOpenEdit.style.pointerEvents = 'initial';
        return false;
    }
    editExpNote() {
        let editForm = document.getElementById('expnote-edit-content');
        let contentDiv = document.getElementById('expnote-content');
        editForm.style.display = 'none';
        contentDiv.style.display = 'block';
        return false;
    }
    deleteExpNote() {
        let answer = confirm('Do you really want to delete this experiment note ?');
        if (answer) {
            this.router.navigate([`/patients/${this.account.id}/medrecords/`, this.expNote.medicalRecord.id]);
        }
        return false;
    }
}


