import {Component, OnInit} from "@angular/core";
import { ROUTER_DIRECTIVES, Router } from '@angular/router';
import {NgForm} from "@angular/common";
import Logger from "../utils/logger.service";
import {SigninAccount, Account} from "./account.model";
import {AccountService} from "./account.service";

@Component({
   selector: 'signup',
   template: require('./signup.component.html'),
   styles: [
      require('../../sass/pages/signup.scss')
   ],
   directives: [ROUTER_DIRECTIVES],
})

export class SignupComponent implements OnInit{
    private account: Account;

    constructor(private router: Router) {}

    ngOnInit() {
        
        // Check account already signed in
        this.account = JSON.parse(window.localStorage.getItem('account'));
        if (this.account && this.account.role == 0) {
            this.router.navigate([`/patients/appointments`]);
        } else if (this.account && this.account.role == 1) {
            this.router.navigate([`/patients/appointments`]);
        } 
    }

    goToDoctorSignup() {
        this.router.navigate(['/signup/doctor']);
        return false;
    }

    goToPatientSignup() {
        this.router.navigate(['/signup/patient']);
        return false;
    }
}



