import { Component} from "@angular/core";
import { ROUTER_DIRECTIVES, Router } from '@angular/router';
import { Http, Response} from "@angular/http";

import { AppointsComponent } from "./doctor.appoints.component";
import { ProfileComponent } from "./profile.component";
import { AppointDetailsComponent } from "./doctor.appoint.details.component";
import { AppointsTotalComponent } from "./doctor.appoints.activated.component";
import { AppointsPendingComponent } from "./doctor.appoints.pending.component";
import { ConnPatientComponent } from "./connpatient.component";

import { Account } from '../account/account.model';

@Component({
    selector: 'doctor',
    template: require("./doctor.component.html"),
    styles: [
        require("../../sass/pages/doctor.scss")
    ],
    directives: [ROUTER_DIRECTIVES],
    precompile: [ProfileComponent,
                AppointsComponent,
                AppointDetailsComponent,
                AppointsTotalComponent,
                AppointsPendingComponent,
                ConnPatientComponent
                ]
})
export class DoctorComponent {
    private account: Account;

    constructor(private router: Router) { }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));
    }
}


