import {Component, OnInit} from "@angular/core";
import {NgForm} from "@angular/common";
import {Router, ROUTER_DIRECTIVES} from "@angular/router";
import Logger from "../utils/logger.service";
import {SigninAccount, Account} from "./account.model";
import {AccountService} from "./account.service";

import { patientRepo } from "../utils/data/patients";
import { doctorRepo } from "../utils/data/doctors";


import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { ExRequestOptions } from "../../main.ts";

@Component({
   selector: 'signin',
   template: require('./signin.component.html'),
   styles: [
      require('../../sass/pages/signin.scss')
   ], 
   providers: [AccountService],
   directives: [ROUTER_DIRECTIVES]
})

export class SigninComponent implements OnInit{
    private account: Account;
    private model: SigninAccount;
    private isInvalid: boolean;
    private logger: Logger;
    
    constructor(private router: Router, private http: Http,
                private accountService: AccountService) {
        this.logger = new Logger("SigninComponent");
    }
    
    ngOnInit() {
        this.isInvalid = false;
        this.model = new SigninAccount();
        
        // Check account already signed in
        this.account = JSON.parse(window.localStorage.getItem('account'));
        if (this.account && this.account.role == 0) {
            this.router.navigate([`/patients/${this.account.id}/appointments`]);
        } else if (this.account && this.account.role == 1) {
            this.router.navigate([`/patients/${this.account.id}/appointments`]);
        } else {
            this.account = null;
            window.localStorage.setItem('account', null);
            this.router.navigate(['/signin']);
        }

        
        
    }
    private extractData(res: Response) {
        let body = res.json();
        // console.log(res);
        return body;
    }
     private handlerError(error: any) {
        let errMsg: string;
        if (error.message) {
            errMsg = error.message;
        } else if (error.status) {
            errMsg = `${error.status} - ${error.statusText}`;
        } else {
            errMsg = 'Server error';
        }

        // console.log(error);
        return Observable.throw(errMsg);
    }
    signIn() {
        this.logger.log("On submiting....");

        this.account = patientRepo.getPatientByEmail(this.model.email);
        if (!this.account) {
            this.account = doctorRepo.getDoctorByEmail(this.model.email);
        }   
        
        // Set data success
        window.localStorage.setItem("account", JSON.stringify(this.account));

        // Routing
        if (this.account) {
            // this.router.navigate(['/'])
            window.location.reload();
        } else {
            this.account = null;
            window.localStorage.setItem('account', null);
            this.router.navigate(['/signin']);
        }


        // let headers = new Headers({
        //     'Accept-Language': 'en-US',
        //     'Content-Type': 'application/json'
          
        // });
        // //let options = new RequestOptions({ headers: headers });
        // let options = new ExRequestOptions();
        // options.headers = headers;
        // let data = {
        //     "FirstName": "Trong",
        //     "LastName": "Bui",
        //     "Email": "trong.buiquoc5@gmail.com",
        //     "Password": "a1111111",
        //     "Address": "Hanoi, Vietnam"
        // }
        // this.http.post('http://olive.azurewebsites.net/api/account/patient', data , options)
        //     .map(this.extractData)
        //     .catch(this.handlerError)
        //     .subscribe(s => {
        //         console.log(s);
        //     }, e => {
        //         console.log(e);

        //     });
            return false;
    }
    
    get diagnostic() {
        return JSON.stringify(this.model);
    }
    
} 



