import { Component, OnInit } from "@angular/core";
import { ROUTER_DIRECTIVES, Router, ActivatedRoute} from '@angular/router';
import { Http, Response } from "@angular/http";
import { Schedule } from 'primeng/primeng';

import { AppointsPendingComponent } from "./patient.appoints.pending.component";
import { AppointsTotalComponent } from "./patient.appoints.activated.component";

import { AppointmentOutput } from "./patient.model";
import { PatientAppointmentsServive } from "./patient.appoints.service";
import { Account } from "../account/account.model";

import { appointRepo } from "../utils/data/appoints";

@Component({
    selector: 'patient-appoint-browser',
    template: require("./patient.appoint.browser.component.html"),
    styles: [
        require("../../sass/pages/appoint.browser.scss"),
    ],

    providers: [PatientAppointmentsServive],
    directives: [ROUTER_DIRECTIVES],
})
export class AppointBrowserComponent implements OnInit {
    private account: Account;
    private appoints: AppointmentOutput[] = [];

    constructor(private appointService: PatientAppointmentsServive,
        private router: Router,
        private activatedRoute: ActivatedRoute) {

    }

    ngOnInit() {
        this.account = JSON.parse(window.localStorage.getItem('account'));

        this.appoints = appointRepo.getAllAppoints();
        // Set tab styles
        // set active tab
        let liActivatedTabElem = document.getElementById('activated-appoints-tablink').parentElement;
        let liPendingTabElem = document.getElementById('pending-appoints-tablink').parentElement;
        // let liBrowserTabElem = document.getElementById('browser-appoints-tablink').parentElement;
        // liBrowserTabElem.className += ' active';
        liActivatedTabElem.className = liPendingTabElem.className.replace(' active', '');
        liPendingTabElem.className = liPendingTabElem.className.replace(' active', '');
    }
}


