import { BloodSugar } from "../../patient/patient.model";
import { ISO8601DateTimeFormat, ISO8601TimeFormat } from "../constants";

class SugarBloodRepo {
    private sugarBloodArr: BloodSugar[] = [];

    constructor() {
        let hb = new BloodSugar();
        hb.id = '1231e324rt';
        hb.value = 108;
        hb.note = 'Eam amet accusata cu. Et quo modus referrentur, iriure atomorum ullamcorper cu nec';
        hb.time = moment().format(ISO8601DateTimeFormat);
        hb.created = moment().format(ISO8601DateTimeFormat);
        hb.lastModified = moment().format(ISO8601DateTimeFormat);

        let hb2 = new BloodSugar();
        hb2.id = '1231e324rt';
        hb.value = 126;
        hb2.note = ' Dolor repudiandae at mel, timeam mentitum partiendo ea vim. Usu cu case verterem evertitur, id prima hendrerit incorrupte mel. Has ut soluta petentium referrentur, mutat ipsum vocent te pri, mea probo definiebas scripserit et';
        hb2.time = moment().format(ISO8601TimeFormat);
        hb2.created = moment().format(ISO8601DateTimeFormat);
        hb2.lastModified = moment().format(ISO8601DateTimeFormat);

        this.sugarBloodArr.push(hb);
        this.sugarBloodArr.push(hb2);
    }

    getAll() {
        return this.sugarBloodArr;
    }

    getSugarBloodRepoByID(id: string) {
        for (let item of this.sugarBloodArr) {
            if (item.id == id) 
                return item;
        }

        return null;
    }
}

export let sugarBloodRepo = new SugarBloodRepo();


